import { Injectable } from '@angular/core';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import { AlertMessage, AlertParameters, AlertService } from '@nequi/features/commons/presentation';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  AnnualCurrencyExchangeTopException,
  ExceedCreditCeilingException,
  ExceedDebitCeilingException,
  ExpiredRateException,
  NoAccountException,
  PaypalAccountLimitsProblemException,
  PaypalAnnualDebitLimitException,
  PaypalAsyncWithdrawalAccountProblemException,
  PaypalAsyncWithdrawalException,
  PaypalConstraintsProblemException,
  PaypalDebitLimitException,
  PaypalDepositDebitLimitException,
  PaypalSystemDebitLimitException,
  TimeoutReverseFinacleException,
  TransactionInProcessException,
  TransactionProblemException,
  WithdrawalTransactionNotCompletedException
} from '@nequi/features/paypal/core';
import { UnavailabilityException } from '@nequi/features/paypal/exceptions';
import { CardModalService, ToastService } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { FormConfig } from '../pages/form/form.config';
import { FormViewModel } from '../pages/form/form.view-model';

@Injectable()
export class FormService {
  public config = FormConfig;
  public viewModel: FormViewModel;

  private factoryException = {
    [NoAccountException.name]: () => {
      this.showTransactionProblemAlert();
    },
    [TransactionProblemException.name]: () => {
      this.showTransactionProblemAlert();
    },
    [TransactionInProcessException.name]: () => {
      this.onShowAlertError(this.config.i18n.errorMessages.transactionInProcess, null, () =>
        this.navigatorProvider.navigateBack(this.config.routes.closet)
      );
    },
    [UnavailabilityException.name]: () => {
      this.onShowAlertError(this.config.i18n.errorMessages.unavailability, null, () =>
        this.navigatorProvider.navigateBack(this.config.routes.closet)
      );
    },
    [TimeoutReverseFinacleException.name]: () => {
      this.onShowAlertError(this.config.i18n.errorMessages.timeoutReverseFinacle, null, () =>
        this.navigatorProvider.navigateBack(this.config.routes.closet)
      );
    },
    [ExpiredRateException.name]: () => {
      this.onShowAlertError(this.config.i18n.errorMessages.expiredRate, null, () =>
        this.navigatorProvider.back()
      );
    },
    [PaypalDebitLimitException.name]: (error: CoreException) => {
      this.showPaypaLimitAlert(error);
    },
    [PaypalDepositDebitLimitException.name]: () => {
      this.showPaypaLimitAlert();
    },
    [PaypalSystemDebitLimitException.name]: () => {
      this.showPaypaLimitAlert();
    },
    [ExceedDebitCeilingException.name]: () => {
      this.showNequiLimitAlert();
    },
    [ExceedCreditCeilingException.name]: () => {
      this.showNequiLimitAlert();
    },
    [WithdrawalTransactionNotCompletedException.name]: () => {
      // analyticsProvider.views.payPalMAPayPalDebitLimitAlerWithdrawal.track();

      this.onShowAlertError(
        this.config.i18n.errorMessages.nequiDebitLimitCommission,
        null,
        () => {
          // analyticsProvider.events.paypal.tapBreakCeilingWithdrawal.track();
          this.navigatorProvider.navigateForward(this.config.routes.breakCeiling);
        },
        () => {
          // analyticsProvider.events.paypal.tapNoBreakCeilingWithdrawalPayPal.track();
          this.navigatorProvider.back();
        }
      );
    },
    [PaypalConstraintsProblemException.name]: (error: CoreException) => {
      this.onShowAlertError(this.config.i18n.errorMessages.paypalConstraintsProblem, error, () =>
        this.navigatorProvider.back()
      );
    },
    [PaypalAccountLimitsProblemException.name]: (error: CoreException) => {
      this.onShowAlertError(this.config.i18n.errorMessages.paypalAccountLimitsProblem, error, () =>
        this.navigatorProvider.back()
      );
    },
    [PaypalAnnualDebitLimitException.name]: (error: CoreException) => {
      this.onShowAlertError(this.config.i18n.errorMessages.annualDebitLimit, error, () =>
        this.navigatorProvider.back()
      );
    },
    [PaypalAsyncWithdrawalException.name]: () => {
      this.onShowCardModal(this.config.i18n.errorMessages.paypalAsyncWithdrawal, () =>
        this.navigatorProvider.navigateForward(this.config.routes.backpackHomeUrl)
      );
    },
    [PaypalAsyncWithdrawalAccountProblemException.name]: () => {
      this.onShowToastError(this.config.i18n.errorMessages.paypalAsyncWithdrawalAccountProblem);
    },
    [AnnualCurrencyExchangeTopException.name]: (error: CoreException) => {
      this.onShowAlertError(this.config.i18n.errorMessages.annualCurrencyExchangeLimit, error, () =>
        this.navigatorProvider.back()
      );
    },
  };

  constructor(
    private alertService: AlertService,
    private navigatorProvider: NavigatorProvider,
    private cardModalService: CardModalService,
    private toastService: ToastService,
    private translateProvider: TranslateProvider
  ) {}

  public setViewModel(viewModel: FormViewModel) {
    this.viewModel = viewModel;
  }

  public processError(error: CoreException) {
    if (error instanceof ExceedCeilingException) {
      throw error;
    }

    if (this.factoryException[error.constructor.name]) {
      this.factoryException[error.constructor.name](error);

      return;
    }

    this.onShowToastError(error.message);
  }

  private showTransactionProblemAlert() {
    this.onShowAlertError(this.config.i18n.errorMessages.transactionProblem, null, () =>
      this.navigatorProvider.navigateBack(this.config.routes.closet)
    );
  }

  private async onShowCardModal(i18nKey: string, onAccept: Function): Promise<void> {
    const messages = this.translateProvider.get(i18nKey);

    await this.cardModalService.show({
      title: messages.header,
      imgPath: messages.image,
      content: messages.message,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: async () => onAccept?.(),
        },
      ],
    });
  }

  private async onShowAlertError(
    messages: string,
    error?: CoreException,
    onAccept?: Function,
    onCancel?: Function
  ) {
    const message = this.translateProvider.get(messages);

    this.alertService.show({
      messages: {
        message: message?.message ?? error.message,
        header: message.header,
        buttons: message.buttons,
      },
      handlers: {
        accept: async () => onAccept?.(),
        cancel: async () => onCancel?.(),
      },
    });
  }

  private async showPaypaLimitAlert(error?: CoreException) {
    const messages = this.translateProvider.get(
      this.config.i18n.errorMessages.paypalWithdrawalLimit
    );

    const alertOptions = this.getInitAlertParameters(messages);

    alertOptions.messages.message = this.getPaypalLimitAlertMessage(
      error?.message,
      messages.message
    );

    this.alertService.show({
      messages: alertOptions.messages,
      handlers: {
        accept: async () => this.navigatorProvider.back(),
      },
    });
  }

  private getPaypalLimitAlertMessage(errorMessage: string, defaultContent: string) {
    if (errorMessage !== undefined) {
      return errorMessage;
    }

    return createFormat(defaultContent, [
      this.viewModel.formParams.maxAllowedLimitInUSD.toString(),
    ]);
  }

  private async showNequiLimitAlert() {
    // analyticsProvider.views.payPalMANequiDebitLimitAlertWithdrawal.track();
    const messages = this.translateProvider.get(this.config.i18n.errorMessages.nequiDebitLimit);

    await this.cardModalService.show({
      title: messages.header,
      imgPath: messages.image,
      content: messages.message,
      buttons: [
        {
          text: messages.buttons.accept,
          cssClass: 'margin-bottom-16',
          handler: () => {
            this.navigatorProvider.navigateForward(this.config.routes.breakCeiling);
          },
        },
        {
          text: messages.buttons.cancel,
          color: 'medianoche',
          handler: () => {
            // analyticsProvider.events.paypal.tapBreakCeilingWithdrawal.track();
            this.navigatorProvider.back();
          },
        },
        {
          text: messages.buttons.link,
          isLink: true,
          handler: () => {
            // analyticsProvider.events.paypal.tapNoBreakCeilingWithdrawal.track();
            this.navigatorProvider.navigateForward(this.config.routes.moneyPartition);
          },
        },
      ],
    });
  }

  private async onShowToastError(i18nKey: string) {
    const message = this.translateProvider.get(i18nKey);

    await this.toastService.showError(message);
  }

  private getInitAlertParameters(messages: AlertMessage): AlertParameters {
    return {
      messages: {
        header: messages.header,
        message: messages.message,
        buttons: {
          accept: messages.buttons?.accept,
          cancel: messages.buttons?.cancel,
        },
      },
    };
  }
}
