import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { AnalyticsInteractor, AnalyticsType, Transaction } from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  EconomicActivityEntity,
  FeeType,
  PaypalInteractor,
  TransactionType,
  WithdrawalEntity,
  WithdrawalTransaction
} from '@nequi/features/paypal/core';
import { CardModalService, ConfirmInfoPocketProps } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { roundNumber } from '@nequi/utils/number';
import { TranslatePipe } from '@ngx-translate/core';

import { WithdrawalMapper } from '../../mappers/withdrawal.mapper';
import { FormService } from '../../services/form.service';
import { FormConfig } from './form.config';
import { FormViewModel } from './form.view-model';

@Component({
  selector: 'nequi-paypal-form',
  templateUrl: './form.page.html',
  styleUrls: ['./form.page.scss'],
  providers: [FormService],
})
export class FormPage extends TransactionBasePage implements OnInit {
  public config = FormConfig;
  public viewModel = new FormViewModel();
  private transactionId: string;

  constructor(
    private paypalInteractor: PaypalInteractor,
    private router: Router,
    private formService: FormService,
    private currencyPipe: CurrencyPipe,
    private translatePipe: TranslatePipe,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private cardModalService: CardModalService,
    injector: Injector
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    this.formService.setViewModel(this.viewModel);
  }

  public async ngOnInit() {
    this.setInitValues();
    await this.getExchangeNumberList();
  }

  public async getExchangeNumberList() {
    try {
      await this.loadingService.show();

      this.viewModel.economicActivityList = await this.paypalInteractor.getExchangeNumberList(
        this.config.exchangeCodes.exchangeFormCode,
        this.config.exchangeCodes.transactionType.withdrawal
      );

      await this.loadingService.hideWithoutFeedback();
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();
      await this.toastService.showError(error.message);
      this.goBack();
    }
  }

  public calculateValues() {
    if (this.viewModel.amountControl && this.viewModel.formParams.recharge) {
      this.viewModel.amountCOP = this.getAmountCopRounded();

      const feeValue = this.getFeeValueCalculated();

      this.viewModel.commission = this.viewModel.getNegativeCommission(feeValue);

      this.viewModel.totalTransaction = this.getTotalTransaction();

      this.viewModel.moneyRequested = this.viewModel.amountControl.value;
      this.viewModel.rate = this.viewModel.formParams.recharge.rate;
    } else {
      this.viewModel.setZeroValues();
    }
  }

  public async selectIncomes(event: EconomicActivityEntity) {
    if (event.exchangeNumber === this.config.exchangeCodes.tourismCode) {
      const modal = this.translateProvider.get(this.config.i18n.declaration);

      this.cardModalService.show({
        title: modal.title,
        content: modal.content,
        buttons: [
          {
            text: modal.buttons.accept,
            handler: async () => {
              this.checkFormData();
            },
          },
          {
            color: 'medianoche',
            text: modal.buttons.cancel,
            handler: async () => {
              this.viewModel.transactionReason = null;
              this.viewModel.economicActivityControl.reset();
            },
          },
        ],
      });
    }
  }

  public async checkFormData() {
    if (this.viewModel.form.valid) {
      this.transactionId = null;

      if (await this.getPaypalTransactionData()) {
        await this.makeTransaction();
      }
    }
  }

  protected async setOriginMoney(transaction: Transaction): Promise<Transaction> {
    return transaction;
  }

  protected async onTransactionSuccess() {
    const successMessage = this.translateProvider.get(this.config.i18n.successMessage);

    this.setAnalyticsSuccesRecharge();

    this.toastService.showSuccess(successMessage);

    this.navigatorProvider.navigateForward(this.config.routes.backpackHomeUrl);
  }

  protected async onTransactionFailed(error: CoreException) {
    this.formService.processError(error);
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const { title, subtitle, ...messages } = this.translateProvider.get(
      this.config.i18n.bottomSheet.confirm
    );

    this.viewModel.setTransactionReason(
      this.config.i18n.bottomSheet.transactionReason,
      this.translatePipe
    );

    return {
      title: title,
      subtitle: subtitle,
      items: this.viewModel.buildItems(messages, this.currencyPipe),
      confirmLabel: messages.button,
    };
  }

  private setInitValues() {
    const formParams: WithdrawalEntity = this.router.getCurrentNavigation().extras.state?.params;

    if (!formParams) {
      this.navigatorProvider.navigateBack(this.config.routes.paypalHome);

      return;
    }

    this.viewModel.formParams = {
      ...formParams,
      recharge: {
        fee: formParams.recharge.fee,
        rate: formParams.recharge.rate,
        maximumFeeValue: formParams.recharge.maximumFeeValue,
        tax: formParams.recharge.tax,
        feeType: formParams.recharge.feeType ?? FeeType.PERCENTAGE,
      },
    };

    const rate = this.viewModel.formParams.recharge.rate;
    const balance = this.viewModel.formParams.balance.available;

    this.viewModel.setMaxLimitTransfer(
      this.viewModel.formParams.maxLimitTransfer,
      this.viewModel.formParams.balance.available
    );

    this.setAnalyticsInitialIntentionOfRecharge(balance, rate);
    this.viewModel.buildForm();
  }

  private getAmountCopRounded(): number {
    return roundNumber(
      this.viewModel.amountControl.value * this.viewModel.formParams.recharge.rate,
      this.config.balance.currency.decimalsToRound
    );
  }

  private getFeeValueCalculated() {
    const feeValueCalculated = this.paypalInteractor.calculateFee(
      this.viewModel.formParams.recharge,
      this.viewModel.amountCOP
    );

    return feeValueCalculated + feeValueCalculated * this.viewModel.formParams.recharge.tax;
  }

  private getTotalTransaction(): number {
    return roundNumber(
      this.viewModel.amountCOP + this.viewModel.commission,
      this.config.balance.currency.decimalsToRound
    );
  }

  private async makeTransaction() {
    const transaction = new WithdrawalTransaction()
      .withAmount(this.viewModel.amountControl.value)
      .withData(
        new WithdrawalMapper().from({
          form: this.viewModel.form.value,
          transactionId: this.transactionId,
        })
      );

    await super.executeTransaction(transaction);
  }

  private async getPaypalTransactionData() {
    try {
      await this.loadingService.show();
      this.transactionId = this.paypalInteractor.getWithdrawalTransactionId();

      const recharges = await this.paypalInteractor.getRecharges({
        option: TransactionType.WITHDRAWAL,
        amount: this.viewModel.amountControl.value,
        transactionId: this.transactionId,
      });

      this.viewModel.updateRecharges(recharges);
      const valueCop = this.viewModel.amountCOP;
      const valueUsd = this.viewModel.moneyRequested;

      this.setAnalyticsFinalIntentionOfRecharge(valueCop, valueUsd);
      await this.loadingService.hideWithoutFeedback();

      return true;
    } catch (error) {
      await this.loadingService.hide(false);

      this.formService.processError(error);

      return false;
    }
  }

  private async setAnalyticsInitialIntentionOfRecharge(balance, rate) {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.intentionOfRecharge.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.intentionOfRecharge.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.intentionOfRecharge.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.intentionOfRecharge.params.cleverTap,
          [getFormattedDateTimes(), balance, rate]
        ),
      },
    });
  }

  private async setAnalyticsFinalIntentionOfRecharge(valueCop, valueUsd) {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.finalIntentionOfRecharge.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.finalIntentionOfRecharge.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.finalIntentionOfRecharge.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.finalIntentionOfRecharge.params.cleverTap,
          [valueUsd, valueCop, getFormattedDateTimes()]
        ),
      },
    });
  }

  private async setAnalyticsSuccesRecharge() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.successRecharge.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.successRecharge.facebook,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.successRecharge.name },
    });
  }
}
