import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomInputValidators } from '@nequi/features/commons/presentation';
import { ContactEntity } from '@nequi/features/contacts/core';
import { Pocket } from '@nequi/features/money-management/core';

import { SendFromQrConfig } from './send-from-qr.config';

export class SendFromQrViewModel {
  public form: FormGroup;
  public contact: FormControl;
  public amount: FormControl;
  public message: FormControl;
  public pocketControl: FormControl;
  public isLoading: boolean;

  public initForm(contact: ContactEntity, amount?: number, message?: string, pocket?: Pocket) {
    this.createFormGroup(contact, amount, message, pocket);
  }

  private createFormGroup(
    contact: ContactEntity,
    amount?: number,
    message?: string,
    pocket?: Pocket
  ) {
    this.crateControls(contact, amount, message, pocket);
    this.form = new FormGroup({
      pocket: this.pocketControl,
      contact: this.contact,
      amount: this.amount,
      message: this.message,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  private crateControls(
    contact: ContactEntity,
    amount?: number,
    message?: string,
    pocket?: Pocket
  ) {
    this.contact = new FormControl(contact || null, [Validators.required]);
    this.amount = new FormControl(amount, [Validators.required, Validators.min(1)]);
    this.pocketControl = new FormControl(pocket);
    this.message = new FormControl(message, [
      Validators.maxLength(SendFromQrConfig.validators.maxLengthMessage),
      CustomInputValidators.transactionField,
    ]);
  }
}
