/* eslint-disable sonarjs/no-duplicate-string */
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { PaypalConfig } from '../config/paypal.config';
import { PaypalMiddleware } from './middlewares/pay-pal.middleware';
import { FormInfoPage } from './pages/form-info/form-info.page';
import { FormPage } from './pages/form/form.page';
import { HomePage } from './pages/home/home.page';
import { RegistryInfoPage } from './pages/registry-info/registry-info.page';
import { RegistryPage } from './pages/registry/registry.page';

const routes: Routes = [
  {
    path: '',
    data: {
      i18nModuleKey: PaypalConfig.i18n.moduleKey,
    },
    canActivate: [PaypalMiddleware],
  },
  {
    path: 'receive',
    component: HomePage,
  },
  {
    path: 'registry',
    component: RegistryPage,
  },
  {
    path: 'info-registry',
    component: RegistryInfoPage,
  },
  {
    path: 'form-info',
    component: FormInfoPage,
  },
  {
    path: 'form',
    component: FormPage,
  },
  {
    path: 'profile',
    loadChildren: () =>
      import('@nequi/features/profile/presentation').then((m) => m.FeaturesProfileModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(I18nModuleResolver.setup(routes, [PaypalConfig.i18n.moduleKey]))],
  exports: [RouterModule],
})
export class FeaturesPaypalRoutingModule {}
