import {
  BalanceEntity,
  LimitEntity,
  RechargeEntity,
  TransactionType
} from '@nequi/features/paypal/core';

import { BalanceProgressBarType } from './constants/balance-progress-bar-type';
import { HomeConfig } from './home.config';
import { BalanceProgressBar } from './interfaces/balance-progress-bar.interface';

export class HomeViewModel {
  public deposit: LimitEntity = null;
  public withdrawal: LimitEntity = null;

  public maxLimitTransfer: number = null;
  public maxAllowedLimitInUSD: number = null;
  public utilisedAmountInUSD: number = null;

  public hideLimits = false;

  public hasError = false;
  public hasDisabledReceive = false;
  public contextualMessage = HomeConfig.i18n.defaultMessage;

  public progressBarType = BalanceProgressBarType;
  public progressBar: BalanceProgressBar = {
    value: HomeConfig.balance.progress.gradiant.value,
    type: this.progressBarType.INDETERMINATE,
    cssClass: HomeConfig.balance.progress.gradiant.css,
  };

  public balance: BalanceEntity;

  public rechargeMetrics: RechargeEntity;
  public transactionType: TransactionType;

  public getProgressValue(value: number, targetValue: number) {
    this.progressBar.type = this.progressBarType.DETERMINATE;
    this.progressBar.value = value / targetValue > 1 ? 1 : value / targetValue;
  }
}
