import { FormControl, FormGroup, Validators } from '@angular/forms';
import { SendToNequiConfigProvider, UserInfoProvider } from '@nequi/features/commons';
import { CustomInputValidators } from '@nequi/features/commons/presentation';

import { SendMoneyState } from '../../entities/send-money.state';
import { SendMoneyUserConfig } from './send-money-user.config';

export class SendMoneyUserViewModel {
  public form: FormGroup;
  public contact: FormControl;
  public amount: FormControl;
  public message: FormControl;
  public pocketControl: FormControl;
  public transactionId: FormControl;
  public isLoading: boolean;
  public userPhoneNumber: string;

  public sendInfo: SendMoneyState;

  private config: SendToNequiConfigProvider;

  constructor(private userInfoProvider: UserInfoProvider) {}

  public get isSendFromRequest(): boolean {
    return !!this.sendInfo?.request?.transactionId;
  }

  public initForm(sendInfo: SendMoneyState) {
    this.sendInfo = sendInfo;
    this.createFormGroup(sendInfo);
    this.userPhoneNumber = this.userInfoProvider.getPhoneNumber();
  }

  public setConfig(config: SendToNequiConfigProvider) {
    this.config = config;
  }

  private createFormGroup(sendInfo?: SendMoneyState) {
    this.crateControls(sendInfo);
    this.form = new FormGroup({
      pocket: this.pocketControl,
      contact: this.contact,
      amount: this.amount,
      message: this.message,
      transactionId: this.transactionId,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  private crateControls(sendInfo?: SendMoneyState) {
    const amount = sendInfo?.request?.amount ?? sendInfo?.amount;
    const contact = sendInfo?.request?.contact ?? sendInfo?.contact;

    this.contact = new FormControl(contact, [Validators.required]);
    this.amount = new FormControl(amount, [
      Validators.required,
      Validators.min(this.config.minAmount),
    ]);

    this.message = new FormControl(sendInfo?.message, [
      Validators.maxLength(SendMoneyUserConfig.validators.maxLengthMessage),
      Validators.minLength(SendMoneyUserConfig.validators.minLengthMessage),
      CustomInputValidators.transactionField,
    ]);

    this.pocketControl = new FormControl(sendInfo?.pocket);
    this.transactionId = new FormControl(sendInfo?.request?.transactionId);
  }
}
