import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import {
  InAppBrowser,
  InAppBrowserEvent,
  InAppBrowserObject
} from '@ionic-native/in-app-browser/ngx';
import {
  AnalyticsInteractor,
  AnalyticsType,
  LocationEntity,
  LoggerInteractor,
  ModuleCode,
  ModuleName,
  OccupationEntity
} from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import { ErrorCode, PaypalEntity } from '@nequi/features/paypal/core';
import { ModuleInfoInteractor } from '@nequi/features/profile/core';
import { CompletedModuleData, ModuleListOrganism } from '@nequi/features/profile/presentation';
import { CardModalService, ToastService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { openUrl, stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { RegistryConfig } from './registry.config';

@Component({
  selector: 'nequi-paypal-registry',
  templateUrl: './registry.page.html',
  styleUrls: ['./registry.page.scss'],
})
export class RegistryPage extends FeatureBasePage implements OnInit {
  @ViewChild('nequiPayPalModuleList')
  private moduleListOrganism: ModuleListOrganism;

  public config = RegistryConfig;

  private browser: InAppBrowserObject;
  private params: PaypalEntity;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private inAppBrowser: InAppBrowser,
    private router: Router,
    private cardModalService: CardModalService,
    private alertService: AlertService,
    private userInfoProvider: UserInfoProvider,
    private moduleInfoInteractor: ModuleInfoInteractor,
    private loggerInteractor: LoggerInteractor,
    private toastService: ToastService,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    this.setAnalyticsIntentionOfBonding();
  }

  public ngOnInit() {
    this.params = this.router.getCurrentNavigation().extras.state as PaypalEntity;
  }

  public onCompletedModuleData(module: CompletedModuleData) {
    if (module.type === ModuleCode.LOCATION) {
      this.locationCompletedHandler(module.data as LocationEntity);
    }

    if (module.type === ModuleCode.OCCUPATION) {
      this.occupationCompletedHandler(module.data as OccupationEntity);
    }

    if (module.type === ModuleCode.REGISTRY_PAYPAL) {
      this.moduleListOrganism.finish();
      this.openWebView();
    }
  }

  public openHelpOption() {
    this.openExternalUrl(this.config.goTo.help);
  }

  public async openWebView() {
    const urlStructure = this.translateProvider.get(this.config.i18n.urlStructure);

    if (!this.params) {
      this.goBack();

      return;
    }

    const phoneNumber = await this.userInfoProvider.getPhoneNumber();

    const url = createFormat(urlStructure, [
      this.params.url,
      this.params.id,
      this.params.returnUrl,
      phoneNumber,
    ]);

    this.browser = this.inAppBrowser.create(
      url,
      '_blank',
      'clearcache=yes,clearsessioncache=yes,location=no,zoom=no'
    );

    this.browser.on('loadstart').subscribe((event) => this.loadStartCallBack(event));
  }

  private setAnalyticsIntentionOfBonding() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.intentionOfBonding.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.intentionOfBonding.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.intentionOfBonding.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.intentionOfBonding.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }

  private setAnalyticsSuccesfulBonding() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.successfullBondging.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.successfullBondging.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.successfullBondging.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.successfullBondging.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }

  private setAnalyticsErrorBonding(errorPaypal) {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.errorBonding.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.errorBonding.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.errorBonding.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.errorBonding.params.cleverTap,
          [errorPaypal]
        ),
      },
    });
  }

  private async locationCompletedHandler(location: LocationEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<LocationEntity>({
        code: ModuleCode.LOCATION,
        data: { key: ModuleName.LOCATION, value: location },
      });

      this.moduleListOrganism.finish();
    } catch (error) {
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private async occupationCompletedHandler(occupation: OccupationEntity) {
    try {
      await this.moduleInfoInteractor.saveModuleInfo<OccupationEntity>({
        code: ModuleCode.OCCUPATION,
        data: { key: ModuleName.OCCUPATION, value: occupation },
      });

      this.moduleListOrganism.finish();
    } catch (error) {
      this.loggerInteractor.error(error);

      await this.toastService.showError(error.message);
    }
  }

  private loadStartCallBack(event: InAppBrowserEvent) {
    const url = new URL(event.url);

    if (
      url.searchParams.get(this.config.searchParams.description) ||
      url.searchParams.get(this.config.searchParams.close)
    ) {
      this.browser.close();
    }

    if (url.href && url.href.indexOf(this.config.goTo.backToDashboard) !== -1) {
      this.browser.close();
      this.openDefaultError();
    }

    if (url.href && url.href.indexOf(this.config.goTo.backToAppAction) !== -1) {
      this.browser.close();
      const payPalResponseCode = url.searchParams.get(this.config.searchParams.responseCode);

      this.manageWebViewUrl(payPalResponseCode);

      if (payPalResponseCode.toLowerCase() === this.config.success) {
        return;
      }

      this.setAnalyticsErrorBonding(payPalResponseCode);
    }
  }

  private async manageWebViewUrl(responseCode: string) {
    if (responseCode?.toLowerCase() === this.config.success) {
      this.showSuccessPaypalRegistration();
      this.setAnalyticsSuccesfulBonding();

      return;
    }

    if (responseCode === ErrorCode.EMAIL_NO_MATCH_WITH_NEQUI) {
      await this.openPopupEmailNotMatch();

      return;
    }

    if (responseCode === ErrorCode.INVALID_COUNTRY) {
      await this.openPopupInvalidCountry();

      return;
    }

    if (responseCode === ErrorCode.EMAIL_ALREADY_LINKED) {
      await this.openPopUpEmailLinked();

      return;
    }

    this.openDefaultError();
  }

  private async showSuccessPaypalRegistration() {
    const messages = this.translateProvider.get(this.config.i18n.successEmailRegistration);

    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.content,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: () => {
            this.navigatorProvider.navigateRoot(this.config.goTo.getMoneyUrl, {
              state: { isRegisterFinished: true },
            });
          },
        },
      ],
    });
  }

  private async openPopupEmailNotMatch() {
    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.emailNotMatch,
      handlers: {
        accept: () => this.goBack(),
        cancel: () => this.openExternalUrl(this.config.goTo.emailNotMatchWithNequiHelpUrl),
      },
    });
  }

  private async openPopupInvalidCountry() {
    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.invalidCountry,
      handlers: {
        accept: () => this.openWebView(),
        cancel: () => this.openExternalUrl(this.config.goTo.createAccountUrl),
      },
    });
  }

  private async openPopUpEmailLinked() {
    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.mailAlreadyLinked,
      handlers: {
        accept: () => this.openWebView(),
      },
    });
  }

  private async openDefaultError() {
    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.defaultError,
      handlers: {
        accept: () => this.goBack(),
      },
    });
  }

  private openExternalUrl(url: string) {
    openUrl(url);
  }
}
