import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CeilingWithMonthlyLimitsPage } from '@nequi/features/account-ceiling/presentation';
import { ModuleWithProvidersFactory } from '@nequi/features/commons';

import { CASH_IN_MODULE_CONFIG } from '../../features/cash-in';
import { CASH_OUT_MODULE_CONFIG } from '../../features/cash-out/index';
import { NOTIFICATIONS_CONFIG } from '../../features/notifications';
import { PAYMENTS_MODULE_CONFIG } from '../../features/payments';
import { SEND_MDOULE_CONFIG } from '../../features/send';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('@nequi/apps/base/mobile').then((m) => m.AppsBaseMobileModule),
  },
  {
    path: 'profile',
    loadChildren: () =>
      import('@nequi/features/profile/presentation').then((m) => m.FeaturesProfileModule),
  },
  {
    path: 'office-withdrawal',
    loadChildren: () =>
      import('@nequi/features/office-withdrawal/presentation').then(
        (m) => m.FeaturesOfficeWithdrawalModule
      ),
  },
  {
    path: 'money-management',
    loadChildren: () =>
      import('@nequi/features/money-management/presentation').then(
        (m) => m.FeaturesMoneyManagementModule
      ),
  },
  {
    path: 'send',
    loadChildren: () =>
      import('@nequi/features/send/presentation').then(
        (m) => new ModuleWithProvidersFactory(m.FeaturesSendModule.forChild(SEND_MDOULE_CONFIG))
      ),
  },
  {
    path: 'send/bank-recharge',
    loadChildren: () =>
      import('@nequi/features/send/presentation').then((m) => m.FeaturesSendModule),
  },
  {
    path: 'cash-in',
    loadChildren: () =>
      import('@nequi/features/cash-in/presentation').then(
        (m) =>
          new ModuleWithProvidersFactory(m.FeaturesCashInModule.forChild(CASH_IN_MODULE_CONFIG))
      ),
  },
  {
    path: 'payments',
    loadChildren: () =>
      import('@nequi/features/payments/presentation').then(
        (m) =>
          new ModuleWithProvidersFactory(m.FeaturePaymentsModule.forChild(PAYMENTS_MODULE_CONFIG))
      ),
  },
  {
    path: 'credits',
    loadChildren: () =>
      import('@nequi/features/credits/presentation').then((m) => m.FeaturesCreditsModule),
  },
  {
    path: 'card',
    loadChildren: () =>
      import('@nequi/features/card/presentation').then((m) => m.FeaturesCardModule),
  },
  {
    path: 'request-money',
    loadChildren: () =>
      import('@nequi/features/request-money/presentation').then((m) => m.RequestMoneyModule),
  },
  {
    path: 'cash-out-money',
    loadChildren: () =>
      import('@nequi/features/cash-out-money/presentation').then(
        (m) => new ModuleWithProvidersFactory(m.CashOutMoneyModule.forChild(CASH_OUT_MODULE_CONFIG))
      ),
  },
  {
    path: 'dynamic-form',
    loadChildren: () =>
      import('@nequi/features/dynamic-form/presentation').then((m) => m.DynamicFormModule),
  },
  {
    path: 'recharge',
    loadChildren: () =>
      import('@nequi/features/recharge/presentation').then((m) => m.FeaturesRechargeModule),
  },
  {
    path: 'one-doc-three',
    loadChildren: () =>
      import('@nequi/features/one-doc-three/presentation').then((m) => m.OneDocThreeModule),
  },
  {
    path: 'paypal',
    loadChildren: () =>
      import('@nequi/features/paypal/presentation').then((m) => m.FeaturesPaypalModule),
  },
  {
    path: 'nfc-civica',
    loadChildren: () =>
      import('@nequi/features/nfc-civica/presentation').then((m) => m.FeaturesNfcCivicaModule),
  },
  {
    path: 'soat',
    loadChildren: () =>
      import('@nequi/features/soat/presentation').then((m) => m.FeaturesSoatModule),
  },
  {
    path: 'codensa',
    loadChildren: () =>
      import('@nequi/features/codensa/presentation').then((m) => m.FeaturesCodensaModule),
  },
  {
    path: 'netflix',
    loadChildren: () =>
      import('@nequi/features/netflix/presentation').then((m) => m.FeaturesNetflixModule),
  },
  {
    path: 'open-banking',
    loadChildren: () =>
      import('@nequi/features/open-banking/presentation').then((m) => m.FeaturesOpenBankingModule),
  },
  {
    path: 'full-registry',
    loadChildren: () =>
      import('@nequi/features/full-registry/presentation').then(
        (m) => m.FeaturesFullRegistryModule
      ),
  },
  {
    path: 'redeem-code',
    loadChildren: () =>
      import('@nequi/features/redeem-code/presentation').then((m) => m.FeaturesRedeemCodeModule),
  },
  {
    path: 'biometry/selfie',
    loadChildren: () =>
      import('@nequi/features/security/biometry/presentation').then((m) => m.SelfieModule),
  },
  {
    path: 'backpack/ceiling',
    component: CeilingWithMonthlyLimitsPage,
  },
  {
    path: 'remittances',
    loadChildren: () =>
      import('@nequi/features/remittances/presentation').then((m) => m.FeaturesRemittancesModule),
  },
  {
    path: 'notifications',
    loadChildren: () =>
      import('@nequi/features/notifications/presentation').then(
        (m) =>
          new ModuleWithProvidersFactory(
            m.FeaturesNotificationsModule.forChild(NOTIFICATIONS_CONFIG)
          )
      ),
  },
  {
    path: 'experiment-alternate-flows',
    loadChildren: () =>
      import('@nequi/features/alternate-flows/presentation').then(
        (m) => m.FeaturesAlternateFlowsModule
      ),
  },
  {
    path: '**',
    redirectTo: '/admin',
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminRoutingModule {}
