import { Injectable } from '@angular/core';
import { CanActivate, UrlTree } from '@angular/router';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import { FirstTimeMessageBottomSheetService } from '@nequi/ui';

import { HomeConfig } from '../pages/home/home.config';
import { AccountService } from '../services/account.service';

@Injectable()
export class PaypalMiddleware implements CanActivate {
  constructor(
    private applicationDataInteractor: ApplicationDataInteractor,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private translateProvider: TranslateProvider,
    private accountService: AccountService,
    private navigatorProvider: NavigatorProvider,
    private moduleConfigProvider: ModuleConfigProvider
  ) {}

  public async canActivate(): Promise<boolean | UrlTree> {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);

    if (await this.checkBottomSheetVisualization()) {
      const { data } = await this.showBottomSheet();

      if (!data) {
        return;
      }

      return this.getUserStatus();
    }

    return this.getUserStatus();
  }

  private async getUserStatus() {
    const data = await this.accountService.getUserStatus();

    if (data) {
      this.accountService.userInfo = data;
      this.navigatorProvider.navigateForward(HomeConfig.routes.infoPage, {
        state: { data },
      });
    }

    return false;
  }

  private async checkBottomSheetVisualization() {
    const shouldNotShowFirstMessage = await this.applicationDataInteractor.findValue<boolean>(
      this.moduleConfigProvider.applicationData.paypalHomeIsShowFirstMessage,
      false
    );

    if (!shouldNotShowFirstMessage) {
      return true;
    }

    return false;
  }

  private async showBottomSheet() {
    const bottomSheet = this.translateProvider.get(HomeConfig.i18n.bottomSheet.firstUsageMessage);

    return this.firstTimeMessageBottomSheetService.show({
      icon: bottomSheet.icon,
      message: bottomSheet.message,
      content: bottomSheet.content,
      cssClass: HomeConfig.bottomSheet.cssClass,
      button: {
        label: bottomSheet.button,
        fill: HomeConfig.bottomSheet.button.fill,
        color: HomeConfig.bottomSheet.button.color,
        action: () => this.hideBottomSheet(true),
      },
      header: {
        view: true,
        action: () => this.hideBottomSheet(false),
      },
    });
  }

  private async hideBottomSheet(canNavigate: boolean) {
    await this.applicationDataInteractor.save(
      this.moduleConfigProvider.applicationData.paypalHomeIsShowFirstMessage,
      true
    );

    return this.firstTimeMessageBottomSheetService.hide(canNavigate);
  }
}
