import { CurrencyPipe } from '@angular/common';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import {
  EconomicActivityEntity,
  RechargeEntity,
  WithdrawalEntity
} from '@nequi/features/paypal/core';
import { ConfirmInfoPocketItem } from '@nequi/ui';
import { roundNumber } from '@nequi/utils/number';
import { TranslatePipe } from '@ngx-translate/core';

import { FormConfig } from './form.config';

export class FormViewModel {
  public form: FormGroup;
  public economicActivityControl: FormControl;
  public amountControl: FormControl;
  public economicActivityList: EconomicActivityEntity[] = [];
  public formParams: WithdrawalEntity;
  public moneyRequested = 0;
  public rate = 0;
  public amountCOP = 0;
  public commission = 0;
  public totalTransaction = 0;
  public maxAmount: number;
  public transactionReason: string;

  public buildForm() {
    this.createControls();
    this.form = new FormGroup({
      economicActivity: this.economicActivityControl,
      amount: this.amountControl,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  public buildItems(messages: any, currencyPipe: CurrencyPipe): Array<ConfirmInfoPocketItem> {
    return [
      {
        label: messages.totalAmount,
        value: currencyPipe.transform(this.totalTransaction),
        isMainValue: true,
      },
      {
        label: messages.moneyRequested,
        value: currencyPipe.transform(this.moneyRequested, '', FormConfig.dollarPrefix, '.00'),
      },
      {
        label: messages.exchangeRate.label,
        value: currencyPipe.transform(this.rate),
        description: messages.exchangeRate.info,
      },
      {
        label: messages.moneyRequestedPesos,
        value: currencyPipe.transform(this.amountCOP),
      },
      {
        label: messages.commission.label,
        value: currencyPipe.transform(this.commission),
        description: messages.commission.info,
      },
      {
        label: messages.economicActivity,
        value: this.transactionReason,
      },
    ];
  }

  public setMaxLimitTransfer(maxLimitTransfer: number, payPalBalance: number) {
    if (maxLimitTransfer > payPalBalance) {
      this.maxAmount = payPalBalance;

      return;
    }

    this.maxAmount = maxLimitTransfer;
  }

  public setTransactionReason(textBaseKey: string, translatePipe: TranslatePipe) {
    this.transactionReason = translatePipe.transform(textBaseKey, {
      economicActivity: this.economicActivityControl.value.description,
      formValue: this.economicActivityControl.value.form,
      formNumber: this.economicActivityControl.value.exchangeNumber,
    });
  }

  public setZeroValues() {
    this.amountCOP = 0;
    this.commission = 0;
    this.totalTransaction = 0;
  }

  public updateRecharges(recharges: RechargeEntity) {
    this.moneyRequested = recharges.originalAmount;
    this.totalTransaction = recharges.totalAmount;
    this.amountCOP = recharges.amount;
    this.commission = this.getNegativeCommission(recharges.totalFeeAndTax);
    this.rate = recharges.rate;
  }

  public getNegativeCommission(feeValue: number): number {
    return roundNumber(feeValue, FormConfig.balance.currency.decimalsToRound) * -1;
  }

  private createControls() {
    this.economicActivityControl = new FormControl(null, [Validators.required]);
    this.amountControl = new FormControl(null, [
      Validators.required,
      Validators.min(1),
      Validators.max(this.maxAmount),
    ]);
  }
}
