import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';

import { FormInfoConfig } from './form-info-config';

@Component({
  selector: 'nequi-form-info',
  templateUrl: './form-info.page.html',
  styleUrls: ['./form-info.page.scss'],
})
export class FormInfoPage implements OnInit {
  public config = FormInfoConfig;
  public params;
  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    private router: Router
  ) {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public ngOnInit() {
    this.params = this.router.getCurrentNavigation().extras.state?.params;
  }

  public goBack() {
    this.navigatorProvider.navigateBack(this.config.routes.paypalHome);
  }

  public goPaypalForm() {
    this.navigatorProvider.navigateForward(this.config.routes.paypalForm, {
      state: {
        params: this.params,
      },
    });
  }
}
