import { CurrencyPipe } from '@angular/common';
import { AfterContentInit, Component, Injector, Input, OnInit } from '@angular/core';
import { TranslateProvider } from '@nequi/features/commons';
import { Attribute, ParameterInteractor } from '@nequi/features/commons/core';
import { TransfiYaInteractor } from '@nequi/features/transfi-ya/core';
import {
  AsyncBoardingMessageOrganism,
  AsyncBoardingMessageProps,
  BottomSheetController,
  NavRootBottomSheetBaseComponent
} from '@nequi/ui';
import { LoadingService, ToastService } from '@nequi/ui/services';
import { createFormat } from '@nequi/utils/string';
import { TranslatePipe } from '@ngx-translate/core';

import { SendConfig } from '../../config';
import { SendOptions } from '../../constants/send-options';

@Component({
  selector: 'app-send-options',
  templateUrl: './send-options.organism.html',
  styleUrls: ['./send-options.organism.scss'],
  providers: [TranslatePipe],
})
export class SendOptionsOrganism
  extends NavRootBottomSheetBaseComponent
  implements AfterContentInit, OnInit {
  @Input() public idOptions: Array<string>;
  public options = [];

  constructor(
    protected bottomSheetController: BottomSheetController,
    private translateProvider: TranslateProvider,
    private currencyPipe: CurrencyPipe,
    private transfiYaInteractor: TransfiYaInteractor,
    private parameterInteractor: ParameterInteractor,
    private loadingService: LoadingService,
    private toastService: ToastService,
    injector: Injector
  ) {
    super(injector);
    this.bottomSheetController = bottomSheetController;
  }

  public ngAfterContentInit() {
    this.options = this.getOptions();
  }

  public ngOnInit() {
    this.options = this.getOptions();
  }

  public close() {
    this.bottomSheetController.dismiss(null);
  }

  public async onOptionClicked(option) {
    switch (option.id) {
      case SendOptions.OTHER_BANK:
        this.bottomSheetController.dismiss({
          url: SendConfig.routes.sendBankInfo,
          information: await this.getOtherBankInformation(),
        });

        break;
      case SendOptions.OTHER_BANK_WITHOUT_DOCUMENT:
        this.bottomSheetController.dismiss({
          url: SendConfig.routes.sendBankInfo,
          information: await this.getOtherBankInformationWithoutDocument(),
        });

        break;
      case SendOptions.TRANSFI_YA:
        this.redirectToTransfiYa();
        break;
      default:
        this.bottomSheetController.dismiss({ url: SendConfig.routes.sendNequi, information: null });
    }
  }

  private async getOtherBankInformation() {
    const CURRENCY_DIGITS = '.0';
    const commission = await this.getCommission();

    const amount = this.currencyPipe
      .transform(commission, '', '', CURRENCY_DIGITS)
      .replace(/\s/g, '');
    const messages = this.translateProvider.get(SendConfig.i18n.information.data);

    return {
      title: messages.title,
      description: createFormat(messages.costs, [amount]),
      infoMessages: SendConfig.i18n.information.infoMessages,
      button: {
        label: messages.button,
        url: SendConfig.routes.sendBank,
      },
    };
  }

  private async redirectToTransfiYa(): Promise<void> {
    this.forward(AsyncBoardingMessageOrganism, {
      content: () => this.getTransfiYaContent(),
      header: {
        goBack: {
          icon: 'arrow-back-outline',
          action: () => this.back(),
        },
      },
      button: {
        label: this.translateProvider.get(SendConfig.i18n.transfiYa.button),
        color: 'guayaba',
        fill: 'outline',
        action: async () => {
          this.bottomSheetController.dismiss({
            url: SendConfig.routes.sendTransfiYa,
            information: null,
          });
        },
      },
    });
  }

  private async getTransfiYaContent(): Promise<AsyncBoardingMessageProps> {
    const limits = await this.transfiYaInteractor.getLimits();
    const helpUrl = await this.transfiYaInteractor.getHelpUrl();

    return {
      title: this.translateProvider.get(SendConfig.i18n.transfiYa.title),
      description: this.translateProvider.get(SendConfig.i18n.transfiYa.description, {
        timeLimit: limits.maxHoursToAcceptTransaction,
      }),
      image: this.translateProvider.get(SendConfig.i18n.transfiYa.image),
      url: {
        link: helpUrl,
        label: this.translateProvider.get(SendConfig.i18n.transfiYa.help) as string,
      },
    };
  }

  private getOptions() {
    if (this.idOptions) {
      return SendConfig.sendOptions.filter(
        (option: any) => this.idOptions.findIndex((el) => el === option.id) !== -1
      );
    }
  }

  private async getCommission() {
    try {
      this.loadingService.show();

      const commission = (
        await this.parameterInteractor.getParameter<Attribute[]>(SendConfig.commissionParameterId)
      )
        .map((param: Attribute) => Number(param.value))
        .reduce((param) => param);

      this.loadingService.hideWithoutFeedback();

      return commission;
    } catch (error) {
      this.loadingService.hideWithoutFeedback();
      this.toastService.showError(error.message);
    }
  }

  private async getOtherBankInformationWithoutDocument() {
    const messages = this.translateProvider.get(SendConfig.i18n.informationWithoutDocument.data);

    return {
      title: messages.title,
      description: null,
      infoMessages: SendConfig.i18n.informationWithoutDocument.infoMessages,
      button: {
        label: messages.ready,
        url: SendConfig.routes.sendBankWithoutDocument,
      },
    };
  }
}
