import { BaseMapper } from '@nequi/features/commons';
import { SendMoneyOtherBankEntity } from '@nequi/features/send/core';

export class SendMoneyOtherBankMapper extends BaseMapper<SendMoneyOtherBankEntity> {
  public from(formValue): SendMoneyOtherBankEntity {
    return {
      favorite: formValue.isFavorite,
      bankAccount: {
        bank: formValue.bank,
        number: formValue.accountNumber,
        type: formValue.accountType,
        holder: {
          name: formValue.name,
          lastName: null,
        },
      },
      message: formValue.message,
      applicationDate: formValue.applicationDate,
    };
  }
}
