import { Subscription } from 'rxjs';

import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import { ModuleConfigProvider, SendOtherBankConfigProvider } from '@nequi/features/commons';
import {
  AccountType,
  AnalyticsInteractor,
  AnalyticsType,
  NOT_BALANCE_AVAILABLE_ERRORS,
  Transaction
} from '@nequi/features/commons/core';
import { AlertParameters, TransactionBasePage } from '@nequi/features/commons/presentation';
import { BalanceInteractor } from '@nequi/features/money-management/core';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';
import { SendConfigProvider } from '@nequi/features/send/config';
import {
  AccountTypeEntity,
  BankEntity,
  BankInteractor,
  FavoriteEntity,
  FeeInteractor,
  FeeTypes,
  SendInteractor,
  SendMoneyOtherBankTransaction
} from '@nequi/features/send/core';
import { ConfirmInfoPocketProps } from '@nequi/ui';
import { getNextAvailableDay, isToday, isTomorrow } from '@nequi/utils/date';
import { createFormat } from '@nequi/utils/string';

import { SendMoneyOtherBankConfig } from '../../send-money-other-bank.config';
import { LoadFavoriteAccountService } from '../../services/load-favorite-account.service';
import { SendMoneyOtherBankMapper } from './mappers/send-money-other-bank.mapper';
import { SendMoneyOtherBankViewModel } from './send-money-other-bank.view-model';

@Component({
  selector: 'app-send-mony-other-bank',
  templateUrl: './send-money-other-bank.page.html',
  styleUrls: ['./send-money-other-bank.page.scss'],
})
export class SendMoneyOtherBankPage extends TransactionBasePage implements OnInit, OnDestroy {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;
  public viewModel = new SendMoneyOtherBankViewModel();
  public balanceToogle = false;
  public commission: number;
  public currentAmount: 0;
  public config: SendOtherBankConfigProvider;

  private favoriteSubscription$: Subscription;

  constructor(
    private bankInteractor: BankInteractor,
    private feeInteractor: FeeInteractor,
    private currencyPipe: CurrencyPipe,
    private loadFavoriteAccountService: LoadFavoriteAccountService,
    private sendInteractor: SendInteractor,
    private router: Router,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private sendConfigProvider: SendConfigProvider,
    injector: Injector,
    private balanceInterator: BalanceInteractor
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey).then(() => {
      this.createDocumentTypes();
    });
  }

  public get accountTypes(): AccountTypeEntity[] {
    return this.viewModel.bankControl?.value?.accountTypes || [];
  }

  public async ngOnInit() {
    const pocket = this.router.getCurrentNavigation().extras.state?.pocket;

    this.config = this.sendConfigProvider.sendOtherBankConfig;
    this.viewModel.setConfig(this.config);
    this.viewModel.buildForm(pocket);

    this.checkLoadFavorite();

    this.favoriteSubscription$ = this.viewModel.isFavoriteControl?.valueChanges?.subscribe?.(
      (value) => {
        this.onFavoriteToggleChange(value);
      }
    );
  }

  public ngOnDestroy() {
    this.viewModel.favoriteAccountSubscription.unsubscribe();
    this.favoriteSubscription$?.unsubscribe();
    this.viewModel.destroy();
  }

  public onBlur() {
    this.viewModel.onBlur();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public async bankSelected(bank: BankEntity) {
    this.viewModel.accountTypeControl.reset();
    this.viewModel.accountNumberControl.reset();
    await this.getFee(bank);
    this.showCostAndDateToSendMoneyNotification(bank);
  }

  public async accountSelected(accountSelected: AccountTypeEntity) {
    this.viewModel.updateAccountControlValidators(accountSelected.attributes);

    if (accountSelected.type === AccountType.CAT) {
      await this.getMaxMoneyToTransfer();

      return;
    }

    this.viewModel.maxMoneyToTransferErrorMessage = this.translateProvider.get(
      SendMoneyOtherBankConfig.i18n.defaultAmountError
    );

    this.viewModel.updateAmountControlValidator(null);
  }

  public async onFavoriteToggleChange(value: boolean) {
    if (value && !this.viewModel.favoriteLoaded) {
      const message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.favoriteActivated);

      await this.toastService.showSuccess(message);
    }

    if (!value && this.viewModel.favoriteLoaded && this.viewModel.isSameAccount) {
      this.showConfirmDeleteFavoriteAlert();
    }
  }

  public goToFavoriteAccount() {
    this.navigatorProvider.navigateForward(SendMoneyOtherBankConfig.favoriteAccountsUrl);
  }

  public async sendMoney(): Promise<void> {
    const transaction = new SendMoneyOtherBankTransaction()
      .withAmount(this.viewModel.form.value.amount)
      .withData(new SendMoneyOtherBankMapper().from(this.viewModel.form.value))
      .withPocket(this.viewModel.form.value.pocket);

    super.executeTransaction(transaction);
  }

  public async getBankList() {
    return this.bankInteractor.getList();
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.confirmation);

    return {
      title: messages.title,
      items: this.viewModel.buildArrayConfirmationItems(messages, this.currencyPipe),
      confirmLabel: messages.buttons.confirm,
      cancelLabel: messages.buttons.cancel,
      viewCancel: false,
    };
  }

  protected async setOriginMoney(transaction: Transaction) {
    if (transaction.pocket === null) {
      const pocket = this.originMoneyService.getAvailablePocket();

      transaction.withPocket(pocket);
    }

    return transaction;
  }

  protected async onTransactionSuccess(_response: void, transaction: Transaction) {
    const message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.success);

    this.setAnalytics();
    await this.toastService.showSuccess(message);
    this.navigatorProvider.navigateForward([SendMoneyOtherBankConfig.routes.detail], {
      state: transaction,
    });
  }

  protected async onTransactionFailed(error: CoreException<{ balance: number }>): Promise<void> {
    if (
      error instanceof ExceedCeilingException ||
      NOT_BALANCE_AVAILABLE_ERRORS.includes(error.code)
    ) {
      throw error;
    }

    const messages = this.translateProvider.get(
      SendMoneyOtherBankConfig.errorMessages[error.code] ??
        SendMoneyOtherBankConfig.i18n.errorDefault
    );

    const alertOptions: AlertParameters = {
      messages: {
        header: messages.title,
        message: error?.data?.balance
          ? createFormat(messages.content, [String(error.data.balance)])
          : messages.content,
        buttons: {
          accept: messages.buttons?.okButton,
          cancel: messages.buttons?.cancelButton,
        },
      },
    };

    this.alertService.show(alertOptions);
  }

  private async createDocumentTypes() {
    const documentTypes = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.documentTypes);

    this.viewModel.createDocumentTypeList(documentTypes);
  }

  private async getFee(bank: BankEntity) {
    try {
      const response = await this.feeInteractor.get({
        feeType: FeeTypes.OTHER_BANKS,
        achCode: bank.achCode,
      });

      this.commission = response.totalCommission;
    } catch (error) {
      if (bank.id === this.config.localBank) {
        this.commission = 0;
      } else {
        this.commission = this.config.defaultCommission;
      }
    }
  }

  private async showCostAndDateToSendMoneyNotification(bank: BankEntity) {
    let message = '';

    if (bank.id === this.config.localBank) {
      message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.sendToLocalBank);

      await this.toastService.showInfo(message);

      return;
    }

    const nextAvailableDay = getNextAvailableDay(new Date(), this.config.lastAvailableHour);

    const arrayMessageParams = [this.currencyPipe.transform(this.commission)];

    if (isToday(nextAvailableDay)) {
      message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.sendToday);

      await this.toastService.showInfo(createFormat(message, arrayMessageParams));

      return;
    }

    if (isTomorrow(nextAvailableDay)) {
      message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.sendTomorrow);
    } else {
      const day = nextAvailableDay.getDay();
      const dayName = SendMoneyOtherBankConfig.notificationMessages.days[day].toLowerCase();

      arrayMessageParams.push(dayName);
      arrayMessageParams.push(day);
      message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.sendNextAvailableDay);
    }

    await this.toastService.showInfo(createFormat(message, arrayMessageParams));
  }

  private checkLoadFavorite() {
    this.viewModel.favoriteAccountSubscription = this.loadFavoriteAccountService.favoriteAccount$.subscribe(
      async (favorite: FavoriteEntity) => {
        this.viewModel.setFavorite(favorite);

        await this.getFee(favorite.bank);
        this.showCostAndDateToSendMoneyNotification(favorite.bank);
      }
    );
  }

  private async showConfirmDeleteFavoriteAlert() {
    if (this.viewModel.favoriteLoaded) {
      this.alertService.showByI18n({
        i18nKey: SendMoneyOtherBankConfig.i18n.confirmDeleteFavorite,
        handlers: {
          accept: () => this.deleteFavorite(),
          cancel: () => this.viewModel.isFavoriteControl.setValue(true),
        },
      });
    }
  }

  private async deleteFavorite() {
    try {
      await this.loadingService.show();
      await this.sendInteractor.deleteFavorite({
        beneficiaryDocument: {
          id: this.viewModel.favoriteLoaded.beneficiaryDocument.id,
          type: this.viewModel.favoriteLoaded.beneficiaryDocument.type,
        },
        destinationAccount: this.viewModel.favoriteLoaded.destination.account,
      });

      this.viewModel.favoriteLoaded = null;
      this.loadingService.hide(true);
    } catch (error) {
      this.loadingService.hide(false);
      this.showErrorDeletingFavoriteAlert();
    }
  }

  private async showErrorDeletingFavoriteAlert() {
    await this.alertService.showByI18n({
      i18nKey: SendMoneyOtherBankConfig.i18n.errorDeletingFavorite,
    });
  }

  private setAnalytics() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: SendMoneyOtherBankConfig.analytics.events.successfulMoneySendingToOtherBank,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: SendMoneyOtherBankConfig.analytics.events.facebookMobilePurchase },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: {
        eventName: SendMoneyOtherBankConfig.analytics.events.successfulMoneySendingToOtherBank,
      },
    });
  }

  private async getMaxMoneyToTransfer() {
    this.loadingService.show();
    const maxMoneyToTransfer = (await this.balanceInterator.getLimits()).credit;
    const message = this.translateProvider.get(SendMoneyOtherBankConfig.i18n.catsAmountError);

    this.viewModel.maxMoneyToTransferErrorMessage = createFormat(message, [
      this.currencyPipe.transform(maxMoneyToTransfer, '', '', '.00'),
    ]);

    this.viewModel.updateAmountControlValidator(maxMoneyToTransfer);
    this.loadingService.hide();
  }
}
