import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateProvider } from '@nequi/features/commons';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { Pocket } from '@nequi/features/money-management/core';
import { SendMoneyOtherBankTransaction } from '@nequi/features/send/core';
import { ViewDetailIconType, ViewDetailItem, ViewDetailStatus } from '@nequi/ui';
import { toCamelCaseProperties } from '@nequi/utils/functions';

import { SendMoneyOtherBankConfig } from '../../send-money-other-bank.config';

interface Labels {
  for: string;
  documentNumber: string;
  bank: string;
  accountNumber: string;
  amount: string;
  message?: string;
}

const FIELD_LABELS = 'cashout.bank.detail.fields';

@Component({
  selector: 'app-send-other-bank-detail',
  templateUrl: './detail.page.html',
  styleUrls: ['./detail.page.scss'],
})
export class OtherBankDetailPage extends FeatureBasePage implements OnInit {
  private transaction: SendMoneyOtherBankTransaction;
  private messages: Labels;

  constructor(
    injector: Injector,
    private router: Router,
    private translateProvider: TranslateProvider,
    private currencyPipe: CurrencyPipe
  ) {
    super(injector);

    this.messages = toCamelCaseProperties(this.translateProvider.get(FIELD_LABELS));
  }

  public get status(): ViewDetailStatus {
    return ViewDetailStatus.RED;
  }

  public get icon(): ViewDetailIconType {
    return ViewDetailIconType.ARROW_DOWN;
  }

  public get details(): ViewDetailItem[] {
    if (!this.transaction || !this.messages) {
      return;
    }

    const holderName = [
      this.transaction.data.bankAccount.holder.name,
      this.transaction.data.bankAccount.holder.lastName,
    ]
      .filter((name) => !!name)
      .join(' ');

    const items = [
      { label: this.messages.for, description: holderName },
      {
        label: this.messages.documentNumber,
        description: this.transaction.data.bankAccount.holder.documentNumber,
      },
      { label: this.messages.bank, description: this.transaction.data.bankAccount.bank.name },
      { label: this.messages.accountNumber, description: this.transaction.data.bankAccount.number },
      {
        label: this.messages.amount,
        description: this.currencyPipe.transform(this.transaction.amount),
      },
    ];

    if (this.transaction.data.message) {
      items.push({
        label: this.messages.message,
        description: this.transaction.data.message,
      });
    }

    return items;
  }

  public get pocket(): Pocket {
    return this.transaction?.pocket;
  }

  public ngOnInit(): void {
    this.transaction = this.router.getCurrentNavigation().extras
      .state as SendMoneyOtherBankTransaction;
  }

  public goBack(): void {
    this.accept();
  }

  public accept(): void {
    this.navigatorProvider.navigateBack(SendMoneyOtherBankConfig.routes.admin);
  }
}
