import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FeatureBasePage } from '@nequi/features/commons/presentation';

import { InformationProps } from '../../../entities/information.entity';
import { ItemDetail } from '../../../entities/item-detail.entity';

@Component({
  selector: 'app-other-bank-info',
  templateUrl: './other-bank-info.page.html',
  styleUrls: ['./other-bank-info.page.scss'],
})
export class OtherBankInfoPage extends FeatureBasePage implements OnInit {
  public title: string;
  public description: string;
  public button = { label: '', url: '', color: '', fill: 'solid' };
  public infoMessages: Array<ItemDetail>;
  public commision: number;

  constructor(injector: Injector, private router: Router) {
    super(injector);
  }

  public async ngOnInit() {
    this.commision = 5;

    const data = this.router.getCurrentNavigation().extras.state?.information as InformationProps;

    this.title = data.title;
    this.description = data.description;
    this.button.label = data.button?.label;
    this.button.url = data.button?.url;
    this.infoMessages = data.infoMessages;
  }

  public gotTo() {
    return this.navigatorProvider.navigateForward([this.button.url], {
      replaceUrl: true,
    });
  }
}
