import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateProvider } from '@nequi/features/commons';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  MovementDetail,
  MovementDetailField,
  MovementEntity
} from '@nequi/features/movements/core';
import { SendMoneyUserEntity } from '@nequi/features/send/core';
import {
  SimpleListBottomSheetService,
  SimpleListItem,
  SimpleListProps,
  ToastService,
  ViewDetailIconType,
  ViewDetailItem,
  ViewDetailStatus
} from '@nequi/ui';
import { toCamelCaseProperties } from '@nequi/utils/functions';

import { SendMoneyUserConfig } from '../send-money-user.config';
import { DetailShareOptions } from './detail-share.options';

interface Labels {
  for: string;
  amount: string;
  date: string;
  reference: string;
  message?: string;
  phoneNumber?: string;
}

@Component({
  selector: 'app-transaction-detail',
  templateUrl: './transaction-detail.page.html',
  styleUrls: ['./transaction-detail.page.scss'],
})
export class TransactionDetailPage extends FeatureBasePage implements OnInit {
  public transaction: SendMoneyUserEntity;
  public availablePocket;
  public showToastComponent = true;
  private messages: Labels;

  constructor(
    injector: Injector,
    private router: Router,
    private translateProvider: TranslateProvider,
    private simpleListBottomSheetService: SimpleListBottomSheetService,
    private currencyPipe: CurrencyPipe,
    private toastService: ToastService
  ) {
    super(injector);

    this.messages = toCamelCaseProperties(
      this.translateProvider.get(SendMoneyUserConfig.labels.fieldLabels)
    );
  }

  public get status(): ViewDetailStatus {
    return ViewDetailStatus.RED;
  }

  public get icon(): ViewDetailIconType {
    return ViewDetailIconType.ARROW_DOWN;
  }

  public get details(): ViewDetailItem[] {
    const dateFormat: object = {
      day: 'numeric',
      month: 'long',
      year: 'numeric',
      hour: 'numeric',
      minute: 'numeric',
    };
    const items: ViewDetailItem[] = [
      { label: this.messages.for, description: this.transaction.destination },
      {
        label: this.messages.reference,
        description: this.transaction.reference,
      },
      { label: this.messages.phoneNumber, description: this.transaction.phoneNumberDestination },
      {
        label: this.messages.date,
        description: this.transaction.date.toLocaleDateString('es-CO', dateFormat),
      },
      {
        label: this.messages.amount,
        description: this.currencyPipe.transform(this.transaction.amount),
      },
    ];

    if (this.transaction.message) {
      items.push({
        label: this.messages.message,
        description: this.transaction.message.includes('{@RM/}')
          ? this.transaction.message.split('{@RM/}')[0]
          : this.transaction.message,
      });
    }

    return items;
  }

  public async ngOnInit(): Promise<void> {
    this.transaction = this.router.getCurrentNavigation().extras.state
      .response as SendMoneyUserEntity;

    this.availablePocket = this.router.getCurrentNavigation().extras.state.transaction.pocket;
    this.availablePocket.amount = this.transaction.amount;

    await this.toastService.showInfo(SendMoneyUserConfig.toast.toastInformation);
  }

  public goBack(): void {
    this.accept();
  }

  public accept(): void {
    this.navigatorProvider.navigateBack(SendMoneyUserConfig.routes.admin);
  }

  public async openShareOptions() {
    const optionsBottomSheet: SimpleListItem[] = SendMoneyUserConfig.detailShareOptions;
    const actions = {
      [DetailShareOptions.SHOW_RECEIPT]: () => {
        this.showReceipt(false);
      },
      [DetailShareOptions.SEND_VIA_EMAIL]: () => {
        this.showReceipt(true);
      },
    };

    this.showSimpleListBottomSheet({ items: optionsBottomSheet, actions });
  }

  private showReceipt(isSharingViaEmail: boolean) {
    const movement: MovementEntity = {
      amount: this.transaction.amount,
      createdAt: this.transaction.date,
      transactionId: this.transaction.reference,
      subject: this.transaction.message,
      title: this.transaction.destination,
      transactionParticular: this.transaction.destination,
      name: this.transaction.destination,
      reference: this.transaction.reference,
    };

    const messages = this.translateProvider.get(SendMoneyUserConfig.i18n.detailLabels);
    const movementDetailField: MovementDetailField[] = [
      {
        type: 'text',
        label: messages.for,
        value: this.transaction.destination,
      },
      {
        type: 'text',
        label: messages.phoneNumber,
        value: this.transaction.phoneNumberDestination,
      },
      {
        type: 'currency',
        label: messages.amount,
        value: this.transaction.amount.toString(),
      },
      {
        type: 'creationDate',
        label: messages.date,
        value: this.transaction.date,
      },
    ];

    if (this.transaction.message) {
      const positionIndex = 2;

      movementDetailField.splice(positionIndex, 0, {
        type: 'message',
        label: messages.message,
        value: this.transaction.message.includes('{@RM/}')
          ? this.transaction.message.split('{@RM/}')
          : [this.transaction.message],
      });
    }

    const movementDetail: MovementDetail = {
      movement: movement,
      detail: movementDetailField,
    };

    this.simpleListBottomSheetService.hide();

    this.navigatorProvider.navigateForward(SendMoneyUserConfig.routes.receipt, {
      state: {
        detail: movementDetail,
        isSharingViaEmail,
      },
    });
  }

  private showSimpleListBottomSheet(componentProps: SimpleListProps) {
    componentProps.theme = SendMoneyUserConfig.bottomSheetTheme;
    this.simpleListBottomSheetService.show(componentProps);
  }
}
