import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  FeeType,
  LimitEntity,
  PaypalEntity,
  PaypalInteractor,
  TransactionType,
  WithdrawalEntity
} from '@nequi/features/paypal/core';
import {
  InvalidTokenException,
  LinkWrongCurrencyException,
  NameNoMatchWithNequiException,
  NoAccountException,
  UnavailabilityException
} from '@nequi/features/paypal/exceptions';
import { FirstTimeMessageBottomSheetService, LoadingService, ToastService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { AccountService } from '../../services/account.service';
import { HomeConfig } from './home.config';
import { HomeViewModel } from './home.view-model';

@Component({
  selector: 'nequi-paypal',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage extends FeatureBasePage implements OnInit {
  public config = HomeConfig;
  public viewModel = new HomeViewModel();

  public showBussyIndicator = false;
  public bussyIndicatorError: string;

  public isRegisterFinished: boolean;

  private connectionData: PaypalEntity;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private paypalInterator: PaypalInteractor,
    private alertService: AlertService,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private accountService: AccountService,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private router: Router
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    this.connectionData = this.router.getCurrentNavigation().extras.state.data as PaypalEntity;
    this.isRegisterFinished =  this.router.getCurrentNavigation().extras.state?.isRegisterFinished;
  }

  public async ngOnInit() {
    await this.loadBalance();
  }

  public goBack() {

    let goBackUrl = this.config.routes.infoPage;

    if(this.isRegisterFinished){
      goBackUrl = this.config.routes.closetUrl;
    }

    this.navigatorProvider.navigateBack(goBackUrl,{
      state: {isRegisterFinished: false},
    });

  }

  public async loadBalance() {
    this.showBussyIndicator = true;

    try {
      this.bussyIndicatorError = '';
      this.viewModel.hasDisabledReceive = false;
      this.viewModel.hasError = false;
      this.viewModel.contextualMessage = this.config.i18n.defaultMessage;

      this.viewModel.balance = await this.paypalInterator.getBalance();
      this.setAnalyticsCheckBalance(this.viewModel.balance.available);

      if (this.viewModel.balance.available === 0) {
        this.viewModel.hasDisabledReceive = true;
        const message = this.translateProvider.get(this.config.i18n.balanceZeroError);

        await this.toastService.showError(message, this.config.toastTimer);
      }

      this.getLimitsAccount();
    } catch (error) {
      await this.manageErrorsBalance(error);
    }
  }

  public async getLimitsAccount() {
    try {
      if (this.viewModel.maxAllowedLimitInUSD === null) {

        const response = await this.accountService.metrics;

        await this.getLimitsAccountSuccess(response.responseLimitsAccounts);

        if (this.viewModel.withdrawal.utilisedAmountInUSD >= this.viewModel.maxAllowedLimitInUSD) {
          this.viewModel.hasError = true;
          this.viewModel.hasDisabledReceive = true;
          this.viewModel.contextualMessage = this.config.i18n.limitExceedError;
        }
      }
    } catch (error) {
      this.bussyIndicatorError = error.message;
      this.showBussyIndicator = true;
    }
  }

  public async goToReceivedToPaypal() {
    try {
      if (
        this.viewModel.balance.available > 0 &&
        this.viewModel.withdrawal?.utilisedAmountInUSD < this.viewModel.maxAllowedLimitInUSD
      ) {
        await this.loadingService.show();

        const metrics = await this.accountService.metrics;

        this.viewModel.rechargeMetrics = metrics.responseRates;

        this.getReceivedRechargeSuccess(TransactionType.WITHDRAWAL);
      }
    } catch (error) {
      await this.loadingService.hide(false);
      await this.manageErrorsReceivedRecharge(error);
    }
  }

  public async onUnlinkAccount() {
    this.alertService.showByI18n({
      i18nKey: this.config.i18n.unlinkAccount,
      handlers: {
        accept: () => this.accountService.unlinkAccount(),
      },
    });
  }

  private async manageErrorsBalance(error: CoreException) {
    const balanceErrors = {
      [NoAccountException.name]: () => this.showRetryRegistryError(error),
      [LinkWrongCurrencyException.name]: () => this.showLinkWrongCurrencyError(),
      [InvalidTokenException.name]: () => this.showRetryRegistryError(error),
      [NameNoMatchWithNequiException.name]: () => this.showModalNameNotMatch(),
    };

    if (balanceErrors[error.constructor.name]) {
      return balanceErrors[error.constructor.name]();
    }

    this.showGeneralError(error, this.config.i18n.generalError);
  }

  private async showModalNameNotMatch(){

    await this.loadingService.hideWithoutFeedback();

    this.alertService.showByI18n({
      i18nKey: this.config.i18n.nameNoMatchWhitNequiError,
      handlers: {
        accept: () => this.accountService.unlinkAccount(),
        cancel: () => this.goBack(),
      },
    });

  }

  private showLinkWrongCurrencyError() {
    // TODO: no existe la url en el app actual 'payPalConfig.cards.putMoneyInNequiHelpUrl'

    this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.linkWrongCurrencyError,
      handlers: {
        accept: () => this.navController.back(),
      },
    });
  }

  private showRetryRegistryError(error: CoreException) {
    const data = this.connectionData;

    this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.retryRegistryError,
      handlers: {
        accept: () => {
          this.bussyIndicatorError = error.message;
          this.showBussyIndicator = true;
          this.navigatorProvider.navigateForward([this.config.routes.registryPageUrl], {
            state: { data },
          });
        },
      },
    });
  }

  private async getLimitsAccountSuccess(limistAccount: LimitEntity[]) {
    this.viewModel.deposit = await this.getLimitsForType(
      limistAccount,
      this.config.typeLimitsAccount.deposit
    );

    this.viewModel.withdrawal = await this.getLimitsForType(
      limistAccount,
      this.config.typeLimitsAccount.withdrawal
    );

    if (!this.viewModel.withdrawal) {
      const errorMessage = this.translateProvider.get(this.config.i18n.withdrawalNull);

      throw new CoreException(errorMessage, null);
    }

    const utilisedAmountInUSD = this.viewModel.withdrawal.utilisedAmountInUSD;
    const maxAllowedLimitInUSD = this.viewModel.withdrawal.maxAllowedLimitInUSD;

    this.viewModel.maxLimitTransfer = 0;

    if (maxAllowedLimitInUSD - utilisedAmountInUSD >= 0) {
      this.viewModel.maxLimitTransfer = maxAllowedLimitInUSD - utilisedAmountInUSD;
    }

    this.viewModel.maxAllowedLimitInUSD = maxAllowedLimitInUSD;
    this.viewModel.hideLimits = false;

    this.viewModel.getProgressValue(utilisedAmountInUSD, maxAllowedLimitInUSD);

    this.showBussyIndicator = false;
  }

  private async getLimitsForType(
    limistAccount: LimitEntity[],
    type: string
  ): Promise<LimitEntity | null> {
    const limitFilter = limistAccount.filter((limit) => String(limit.transactionType) === type);

    return limitFilter && limitFilter.length === 1 ? limitFilter[0] : null;
  }

  private async getReceivedRechargeSuccess(type: TransactionType) {
    this.viewModel.transactionType = type;

    const message = this.config.i18n.bottomSheet.sendPayPalToNequi;
    let params = [
      String(this.viewModel.maxAllowedLimitInUSD),
      String(this.viewModel.rechargeMetrics.fee * HomeConfig.percentage),
      String(this.viewModel.rechargeMetrics.maximumFeeValue +
        (this.viewModel.rechargeMetrics.maximumFeeValue * this.viewModel.rechargeMetrics.tax)),
    ];

    if (this.viewModel.rechargeMetrics.feeType === FeeType.FIX_AMOUNT) {
      this.viewModel.rechargeMetrics.fee = this.viewModel.rechargeMetrics.defaultFeeValue;

      params = [
        String(this.viewModel.maxAllowedLimitInUSD),
        String(this.viewModel.rechargeMetrics.fee),
        String(this.viewModel.rechargeMetrics.maximumFeeValue +
          (this.viewModel.rechargeMetrics.maximumFeeValue * this.viewModel.rechargeMetrics.tax)),
      ];
    }

    await this.loadingService.hideWithoutFeedback();

    if (this.viewModel.rechargeMetrics) {
      this.showBottomSheetRecharge(message, params);
    }
  }

  private async showBottomSheetRecharge(message: string, params: string[]) {
    const bottomSheet = this.translateProvider.get(message);

    await this.firstTimeMessageBottomSheetService.show({
      icon: bottomSheet.image,
      message: bottomSheet.title,
      content: createFormat(bottomSheet.content, params),
      cssClass: HomeConfig.bottomSheet.cssClass,
      button: {
        label: bottomSheet.button,
        fill: HomeConfig.bottomSheet.button.fill,
        color: HomeConfig.bottomSheet.button.color,
        action: () => {
          this.firstTimeMessageBottomSheetService.hide();
          this.navigateToPaypalFormInfo();
        },
      },
      header: {
        view: true,
        action: () => this.firstTimeMessageBottomSheetService.hide(),
      },
    });
  }

  private navigateToPaypalFormInfo() {
    const params: WithdrawalEntity = {
      recharge: this.viewModel.rechargeMetrics,
      balance: this.viewModel.balance,
      maxLimitTransfer: this.viewModel.maxLimitTransfer,
      maxAllowedLimitInUSD: this.viewModel.maxAllowedLimitInUSD,
    };

    this.navController.navigateForward(this.config.routes.payPalFormInfo, {
      state: {
        params: params,
      },
    });
  }

  private async manageErrorsReceivedRecharge(error: CoreException) {
    if (error.constructor === UnavailabilityException) {
      this.showGeneralError(error, this.config.i18n.unavailabilityError);

      return;
    }

    await this.toastService.showError(error.message, this.config.toastTimer);
  }

  private async showGeneralError(error: CoreException, messages: string) {
    const message = this.translateProvider.get(messages);

    this.alertService.show({
      messages: {
        message: error.message,
        header: message.header,
        buttons: message.buttons,
      },
      handlers: {
        accept: () => this.navController.navigateBack(this.config.routes.closetUrl),
      },
    });
  }

  private setAnalyticsCheckBalance(balance) {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.checkBalance.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.checkBalance.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.checkBalance.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.checkBalance.params.cleverTap,
          [getFormattedDateTimes(), balance]
        ),
      },
    });
  }
}
