import { Component, OnInit } from '@angular/core';
import { EmptyCaseException } from '@nequi/commons/exceptions';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import {
  AccountTypeEntity,
  BankEntity,
  BankInteractor,
  FavoriteEntity,
  SendInteractor
} from '@nequi/features/send/core';
import { LoadingService, ToastService } from '@nequi/ui';
import { removeAccents, scapeSpecialChars } from '@nequi/utils/string';

import { LoadFavoriteAccountService } from '../../services/load-favorite-account.service';
import { FavoriteAccountConfig } from './favorite-account.config';
import { FavoriteAccountViewModel } from './favorite-account.view-model';

const filterBank = (bankA: BankEntity, code: string): boolean => bankA.achCode === code;
const filterAccountType = (typeAccountA: AccountTypeEntity, code: string): boolean =>
  typeAccountA.code === code;

@Component({
  selector: 'app-favorite-account',
  templateUrl: './favorite-account.page.html',
  styleUrls: ['./favorite-account.page.scss'],
})
export class FavoriteAccountPage implements OnInit {
  public viewModel = new FavoriteAccountViewModel();
  public config = FavoriteAccountConfig;
  public goBackPressed = false;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private sendInteractor: SendInteractor,
    private loadingService: LoadingService,
    private translateProvider: TranslateProvider,
    private loadFavoriteAccountService: LoadFavoriteAccountService,
    private toastService: ToastService,
    private bankInteractor: BankInteractor,
    private moduleConfigProvider: ModuleConfigProvider
  ) {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public async ngOnInit() {
    await this.getFavorites();
  }

  public async getFavorites() {
    try {
      await this.loadingService.show();

      const favorites = await this.sendInteractor.getFavorites();

      this.viewModel.favorites = await this.mapFavorites(favorites);
      this.viewModel.favoritesFiltered = [...this.viewModel.favorites];

      await this.loadingService.hide(true);
    } catch (error) {
      if (error instanceof EmptyCaseException) {
        this.viewModel.isEmptyCase = true;
        this.viewModel.favorites = [];
        this.viewModel.favoritesFiltered = [];

        await this.loadingService.hide(true);

        return;
      }

      await this.loadingService.hide(false);
      this.toastService.showError(error.message);
    }
  }

  public onFavoriteSelected(favorite: FavoriteEntity) {
    this.loadFavoriteAccountService.setFavoriteAccount(favorite);
    this.goBack();
  }

  public filterFavoriteAccounts(event: CustomEvent) {
    const query = scapeSpecialChars(removeAccents(event.detail.value.trim().toLowerCase()));

    if (query.length <= 0) {
      this.viewModel.favoritesFiltered = [...this.viewModel.favorites];

      return;
    }

    this.viewModel.favoritesFiltered = this.viewModel.favorites.filter((account) => {
      const name = scapeSpecialChars(removeAccents(account.destination.name.toLowerCase()));

      const destinationAccount = scapeSpecialChars(
        removeAccents(account.destination.account.toLowerCase())
      );
      const bankName = scapeSpecialChars(removeAccents(account.bank.name.toLowerCase()));

      return (
        name.indexOf(query) >= 0 ||
        destinationAccount.indexOf(query) >= 0 ||
        bankName.indexOf(query) >= 0
      );
    });
  }

  public goBack() {
    this.goBackPressed = true;
    this.navigatorProvider.back();
  }

  private async mapFavorites(favorites: FavoriteEntity[]): Promise<FavoriteEntity[]> {
    const bankList = await this.bankInteractor.getList();

    return favorites.map((favorite) => {
      const bank = bankList.find((currentBank) => filterBank(currentBank, favorite.bank.achCode));

      return {
        ...favorite,
        bank: bank,
        accountType: bank.accountTypes.find((account) =>
          filterAccountType(account, favorite.destination.typeAccount)
        ),
      };
    });
  }
}
