import { Injectable } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import { AlertService } from '@nequi/features/commons/presentation';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  LimitEntity,
  PaypalEntity,
  PaypalInteractor,
  RechargeEntity,
  RechargeParams,
  TransactionType
} from '@nequi/features/paypal/core';
import {
  ControlListException,
  EmailException,
  NoAccountException
} from '@nequi/features/paypal/exceptions';
import { PinChallengeModalService } from '@nequi/features/security/auth/presentation';
import { CardModalService, LoadingService, ToastService } from '@nequi/ui';

import { HomeConfig } from '../pages/home/home.config';

export interface MetricsParams {
  responseRates: RechargeEntity;
  responseLimitsAccounts: LimitEntity[];
}

@Injectable()
export class AccountService {
  private config = HomeConfig;
  private metricsInfo: MetricsParams;
  private infoUser: PaypalEntity;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private paypalInterator: PaypalInteractor,
    private alertService: AlertService,
    private cardModalService: CardModalService,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private translateProvider: TranslateProvider,
    private pinChallengeService: PinChallengeModalService
  ) {}

  public set metrics(params) {
    this.metricsInfo = params;
  }

  public get metrics(): MetricsParams {
    return this.metricsInfo;
  }

  public set userInfo(info) {
    this.infoUser = info;
  }

  public get userInfo(): PaypalEntity {
    return this.infoUser;
  }

  public async getUserStatus(): Promise<PaypalEntity> {
    try {
      await this.loadingService.show();

      const data = await this.paypalInterator.getUserStatus();

      await this.loadingService.hideWithoutFeedback();

      return data;
    } catch (error) {
      return this.processError(error);
    }
  }

  public async unlinkAccount() {
    try {
      await this.loadingService.show();
      await this.paypalInterator.unlinkAccountPaypal();

      await this.loadingService.hide(true);

      const message = this.translateProvider.get(this.config.i18n.unlinkAccountSuccess);

      await this.toastService.showSuccess(message, this.config.toastTimer);

      this.navigatorProvider.navigateRoot(this.config.routes.backpackHomeUrl);
    } catch (error) {
      await this.showUnlinkAccountError();
      await this.loadingService.hide(false);
    }
  }

  public async getAccountServices(): Promise<MetricsParams> {
    const params = {
      option: TransactionType.WITHDRAWAL,
    } as RechargeParams;

    const responseRates = await this.paypalInterator.getRecharges(params);
    const responseLimitsAccounts = await this.paypalInterator.getLimitsAccount(
      this.config.typeLimitsAccount.withdrawal
    );

    this.metrics = { responseRates, responseLimitsAccounts };

    return this.metrics;
  }

  private async showUnlinkAccountError() {
    await this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.failUnlinkAccount,
      handlers: {
        accept: () => this.navigatorProvider.navigateBack(this.config.routes.closetUrl),
      },
    });
  }

  private async processError(error: CoreException) {
    const accountExceptions = {
      [ControlListException.name]: () => this.showControlListError(),
      [EmailException.name]: () => this.showModalEmailConfirmation(),
      [NoAccountException.name]: () => this.registryAccount(error),
    };

    if (accountExceptions[error.constructor.name]) {
      accountExceptions[error.constructor.name]();

      return null;
    }

    await this.loadingService.hideWithoutFeedback();

    throw error;
  }

  private async showControlListError() {
    await this.loadingService.hideWithoutFeedback();

    await this.alertService.showByI18n({
      i18nKey: HomeConfig.i18n.controlListError,
    });
  }

  private async showModalEmailConfirmation() {
    await this.loadingService.hideWithoutFeedback();

    const messages = this.translateProvider.get(this.config.i18n.emailConfirmationError);

    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.content,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: () => this.gotToSettingsToChangeEmail(),
        },
        {
          text: messages.buttons.cancel,
          color: this.config.color.medianocheGray,
        },
      ],
    });
  }

  private async gotToSettingsToChangeEmail() {
    const pinChallengeMessages = this.translateProvider.get(this.config.i18n.pinChallenge);

    if (!(await this.pinChallengeService.show(pinChallengeMessages))) {
      return;
    }

    this.navigatorProvider.navigateForward([this.config.routes.emailUrl], {
      state: { requireKeyCard: true },
    });
  }

  private async registryAccount(error: CoreException) {
    const data = error?.data;

    this.userInfo = data as PaypalEntity;

    await this.loadingService.hideWithoutFeedback();

    this.navigatorProvider.navigateForward([this.config.routes.infoPage], {
      state: { data },
    });

    return true;
  }
}
