import { NgModule, Type } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { SendMoneyHomeMiddleware } from './middleware/send-money-home.middleware';
import { SendFromQrPage } from './nequi/pages/send-from-qr/send-from-qr.page';
import { SendMoneyUserPage } from './nequi/pages/send-money-user.page';
import { TransactionDetailPage } from './nequi/pages/transaction-detail/transaction-detail.page';
import { OtherBankDetailPage } from './other-bank/pages/detail/detail.page';
import { FavoriteAccountPage } from './other-bank/pages/favorite/favorite-account.page';
import { SendMoneyOtherBankPage } from './other-bank/pages/home/send-money-other-bank.page';
import { OtherBankInfoPage } from './other-bank/pages/other-bank-info/other-bank-info.page';
import {
  SendOtherBankWithoutIdPage
} from './other-bank/pages/send-other-bank-without-id/send-other-bank-without-id.page';

const routes: Routes = [
  {
    path: '',
    component: Type,
    canActivate: [SendMoneyHomeMiddleware],
  },
  {
    path: 'nequi',
    component: SendMoneyUserPage,
  },
  {
    path: 'nequi/datail',
    component: TransactionDetailPage,
  },
  {
    path: 'from-qr',
    component: SendFromQrPage,
  },
  {
    path: 'bank',
    component: SendMoneyOtherBankPage,
  },
  {
    path: 'bank-recharge',
    loadChildren: () =>
      import('./bank-recharge/bank-recharge.module').then((m) => m.BankRechargeModule),
  },
  {
    path: 'bank/favorite',
    component: FavoriteAccountPage,
  },
  {
    path: 'bank/detail',
    component: OtherBankDetailPage,
  },
  {
    path: 'transfi-ya',
    loadChildren: () =>
      import('@nequi/features/transfi-ya/presentation').then((m) => m.FeaturesTransfiYaModule),
  },
  {
    path: 'bank-info',
    component: OtherBankInfoPage,
  },
  {
    path: 'other-bank-without-id',
    component: SendOtherBankWithoutIdPage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeaturesSendRoutingModule {}
