import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
  PaymentEntity,
  PaymentTransaction,
  PaymentType,
  Transaction
} from '@nequi/features/commons/core';
import { AlertParameters, TransactionBasePage } from '@nequi/features/commons/presentation';
import { ModuleConfigProvider } from '@nequi/features/commons/providers';
import { ContactEntity, ContactsInteractor } from '@nequi/features/contacts/core';
import {
  QuickPaymentEntity,
  QuickSendFromQrPaymentEntity
} from '@nequi/features/payments/core/entities';
import { SendMoneyUserEntity } from '@nequi/features/send/core';
import { isTypeOf } from '@nequi/utils/types';

import { SendFromQrViewModel } from './send-from-qr-view.model';
import { SendFromQrConfig } from './send-from-qr.config';

@Component({
  selector: 'app-send-money-user',
  templateUrl: './send-from-qr.page.html',
  styleUrls: ['./../send-money-user.page.scss'],
})
export class SendFromQrPage extends TransactionBasePage implements OnInit {
  public viewModel = new SendFromQrViewModel();

  private readonly config = SendFromQrConfig;

  private transactionInfo: QuickPaymentEntity;

  constructor(
    private readonly contactsInteractor: ContactsInteractor,
    private readonly router: Router,
    private moduleConfigProvider: ModuleConfigProvider,
    injector: Injector
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public async ngOnInit() {
    this.transactionInfo = this.router.getCurrentNavigation().extras
      ?.state as QuickPaymentEntity<QuickSendFromQrPaymentEntity>;

    const contact = {
      phoneNumber: this.transactionInfo?.contactInfo?.phoneNumber,
      firstName: this.transactionInfo?.contactInfo?.userName,
      completeName: this.transactionInfo?.contactInfo?.userName,
    } as ContactEntity;

    this.viewModel.initForm(
      contact,
      this.transactionInfo?.amount,
      this.transactionInfo?.description,
      this.transactionInfo?.pocket
    );
  }

  public get minAmount() {
    return this.viewModel.amount.value || 0;
  }

  public async onSendMoney() {
    this.viewModel.isLoading = true;

    try {
      const contact = (
        await this.contactsInteractor.updateStatus([this.viewModel.contact.value])
      )[0];

      if (contact?.isNequi) {
        this.sendMoney(contact);
      } else {
        await this.noNequiAlert();
      }
    } catch (error) {
      this.toastService.showError(error.message);
    }

    this.viewModel.isLoading = false;
  }

  protected async setOriginMoney(transaction: Transaction) {
    if (transaction.pocket === null) {
      const pocket = this.originMoneyService.getAvailablePocket();

      transaction.withPocket(pocket);
    }

    return transaction;
  }

  protected async onTransactionSuccess(
    response: SendMoneyUserEntity | any,
    transaction: PaymentTransaction<
      PaymentEntity<{ name: string; code: string }, QuickPaymentEntity>
    >
  ) {
    if (!transaction.data.additionalInfo.contactInfo.isNequi) {
      await this.noNequiAlert();

      return;
    }

    if (isTypeOf<SendMoneyUserEntity>(response, ['reference', 'amount'])) {
      this.navigatorProvider.navigateBack(this.config.routes.transactionDetail, {
        state: {
          response,
          transaction,
        },
      });
    } else {
      await this.navigatorProvider.navigateForward(this.config.routes.movements);
      this.toastService.showSuccess(this.translateProvider.get(this.config.i18n.successMessage));
    }
  }

  private async sendMoney(contact: ContactEntity): Promise<void> {
    this.transactionInfo.contactInfo = { ...contact };

    const transaction = new PaymentTransaction<
      PaymentEntity<{ name: string; code: string }, unknown>
    >()
      .withAmount(this.viewModel.amount.value)
      .withPocket(this.viewModel.form.value.pocket)
      .withData({
        merchant: {
          name: contact.completeName,
          code: '',
        },
        type: PaymentType.QR,
        additionalInfo: this.transactionInfo,
      });

    super.executeTransaction(transaction);
  }

  private async noNequiAlert() {
    const messages = this.translateProvider.get(this.config.i18n.noNequiAlert);

    const alertOptions: AlertParameters = {
      messages: {
        header: messages?.title,
        message: messages?.content,
        buttons: {
          accept: messages.buttons?.accept,
        },
      },
    };

    this.alertService.show(alertOptions);
  }
}
