import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { NavController } from '@ionic/angular';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons';
import { BottomSheetController, NavRootBottomSheetComponent } from '@nequi/ui';
import { isTypeOf } from '@nequi/utils/types';

import { SendConfigProvider } from '../../config';
import { InformationProps } from '../entities/information.entity';
import { SendOptionsOrganism } from '../organisms/send-options/send-options.organism';

interface NavigationProps {
  information: InformationProps;
  url: string;
}

@Injectable()
export class SendMoneyHomeMiddleware implements CanActivate {
  constructor(
    private bottomSheetController: BottomSheetController,
    private router: Router,
    private navController: NavController,
    private translateProvider: TranslateProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    private sendConfigProvider: SendConfigProvider
  ) {}

  public async canActivate(
    _route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Promise<boolean> {
    const navigationData = await this.openBottomSheet();
    const pocket = this.router.getCurrentNavigation().extras.state?.pocket;

    if (navigationData?.url && isTypeOf<string>(navigationData.url, String)) {
      const urlTree = this.router.createUrlTree([state.url, navigationData.url]);

      this.navController.navigateForward(urlTree, {
        state: { pocket, information: navigationData.information },
      });
    }

    return false;
  }

  private async openBottomSheet(): Promise<NavigationProps> {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);

    const bottomSheet = this.bottomSheetController.create({
      component: NavRootBottomSheetComponent,
      componentProps: {
        rootPage: SendOptionsOrganism,
        inputProps: {
          idOptions: this.sendConfigProvider.sendIdOptions,
        },
      },
    });

    await bottomSheet.open();
    const response = await bottomSheet.onDidDismiss();

    return response.data;
  }
}
