import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import {
  ContactInvitationService
} from '@nequi/apps/base/mobile/config/features/commons/services/contact-invitation';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { PhonePipe } from '@nequi/features/commons/presentation/';
import { FeaturesContactsModule } from '@nequi/features/contacts/presentation';
import { FeaturesMoneyManagementModule } from '@nequi/features/money-management/presentation';
import { BankAccountRepository, SendInteractor } from '@nequi/features/send/core';
import { SocialSharingPlugin } from '@nequi/native/social-sharing';
import { InputSelectBottomSheetService, NequiUiModule } from '@nequi/ui';
import { TranslateModule, TranslatePipe } from '@ngx-translate/core';

import { SendConfigProvider } from '../config';
import { SendConfig } from '../config/send.config';
import { FeaturesSendRoutingModule } from './features-send-routing.module';
import { SendMoneyHomeMiddleware } from './middleware/send-money-home.middleware';
import { SendFromQrPage } from './nequi/pages/send-from-qr/send-from-qr.page';
import { SendMoneyUserPage } from './nequi/pages/send-money-user.page';
import { TransactionDetailPage } from './nequi/pages/transaction-detail/transaction-detail.page';
import { SendOptionsOrganism } from './organisms/send-options/send-options.organism';
import { OtherBankDetailPage } from './other-bank/pages/detail/detail.page';
import { FavoriteAccountPage } from './other-bank/pages/favorite/favorite-account.page';
import { SendMoneyOtherBankPage } from './other-bank/pages/home/send-money-other-bank.page';
import { OtherBankInfoPage } from './other-bank/pages/other-bank-info/other-bank-info.page';
import {
  SendOtherBankWithoutIdPage
} from './other-bank/pages/send-other-bank-without-id/send-other-bank-without-id.page';
import { LoadFavoriteAccountService } from './other-bank/services/load-favorite-account.service';

@NgModule({
  imports: [
    CommonModule,
    IonicModule,
    ReactiveFormsModule,
    FeaturesSendRoutingModule,
    NequiUiModule,
    TranslateModule,
    FeaturesMoneyManagementModule,
    FeaturesContactsModule,
  ],
  declarations: [
    SendOptionsOrganism,
    SendMoneyOtherBankPage,
    SendMoneyUserPage,
    FavoriteAccountPage,
    SendFromQrPage,
    OtherBankDetailPage,
    SendOtherBankWithoutIdPage,
    OtherBankInfoPage,
    TransactionDetailPage,
  ],
  providers: [
    SendMoneyHomeMiddleware,
    InputSelectBottomSheetService,
    ContactInvitationService,
    SocialSharingPlugin,
    TranslatePipe,
    LoadFavoriteAccountService,
    {
      provide: SendInteractor,
      deps: [BankAccountRepository],
      useClass: SendInteractor,
    },
    {
      provide: ModuleConfigProvider,
      useValue: SendConfig,
    },
    PhonePipe,
  ],
})
export class FeaturesSendModule {
  public static forChild(config: SendConfigProvider): ModuleWithProviders<FeaturesSendModule> {
    return {
      ngModule: FeaturesSendModule,
      providers: [
        {
          provide: SendConfigProvider,
          useValue: config,
        },
      ],
    };
  }
}
