import { BaseMapper } from '@nequi/features/commons';
import { WithdrawalParams } from '@nequi/features/paypal/core';

export class WithdrawalMapper extends BaseMapper<WithdrawalParams> {
  public from(params: any): WithdrawalParams {
    return {
      transactionId: params.transactionId,
      description: params.form.economicActivity.description,
      exchangeFormCode: params.form.economicActivity.form,
      exchangeNumeral: params.form.economicActivity.exchangeNumber,
    };
  }
}
