import { Subscription } from 'rxjs';

import { CurrencyPipe } from '@angular/common';
import {
  AbstractControl,
  FormControl,
  FormGroup,
  ValidationErrors,
  ValidatorFn,
  Validators
} from '@angular/forms';
import { SendOtherBankConfigProvider } from '@nequi/features/commons';
import { CustomInputValidators, CustomTextNormalizer } from '@nequi/features/commons/presentation';
import { Pocket } from '@nequi/features/money-management/core';
import {
  AccountTypeEntity,
  AttributeEntity,
  BankEntity,
  FavoriteEntity
} from '@nequi/features/send/core';
import { ConfirmInfoItem } from '@nequi/ui';

import { SendMoneyOtherBankConfig } from '../../send-money-other-bank.config';

const FAVORITE_CONTROL_NAME = 'isFavorite';

const normalizeName = (value): string => value.replace(
  CustomTextNormalizer.regex.NORMALIZE_ACCENTS_AND_SPECIAL_CHARACTERS,
  '\0'
);

type FormDefinition = {
  name: string;
  documentType: { name: string; value: string };
  documentNumber: string;
  bank: BankEntity;
  accountType: AccountTypeEntity;
  accountNumber: string;
  amount: number;
  isFavorite: boolean;
  pocket: Pocket;
};

const filterAccountType = (typeAccountA: AccountTypeEntity, code: string): boolean =>
  typeAccountA.code === code;

const isSameAccount = (favorite: FavoriteEntity, formInfo: FormDefinition): boolean =>
  favorite.beneficiaryDocument.id === formInfo.documentNumber &&
  favorite.beneficiaryDocument.type === formInfo.documentType.value &&
  favorite.bank.achCode === formInfo.bank.achCode &&
  favorite.destination.typeAccount === formInfo.accountType.code &&
  favorite.destination.account === formInfo.accountNumber;

export class SendMoneyOtherBankViewModel {
  public form: FormGroup;
  public nameControl: FormControl;
  public documentTypeControl: FormControl;
  public documentNumberControl: FormControl;
  public bankControl: FormControl;
  public accountTypeControl: FormControl;
  public accountNumberControl: FormControl;
  public amountControl: FormControl;
  public isFavoriteControl: FormControl;
  public pocketControl: FormControl;
  public documentTypes = [];
  public bankList: BankEntity[] = [];
  public favoriteAccountSubscription: Subscription;
  public isFavoriteLoaded = false;
  public favoriteLoaded: FavoriteEntity;
  public maxMoneyToTransferErrorMessage: string;

  private subscriptions$: Subscription[];

  private config: SendOtherBankConfigProvider;

  public onBlur() {
    const fieldNameValue = this.form.value.name;
    const fieldNameValueTransformed = fieldNameValue
      .normalize('NFD')
      .replace(CustomTextNormalizer.regex.NORMALIZE_ACCENTS, '')
      .trim();

    this.form.get('name').setValue(fieldNameValueTransformed);
  }

  public normalizeNameData(formControl: FormControl) {
    formControl.setValue(normalizeName(formControl.value));
  }

  public get isSameAccount(): boolean {
    return isSameAccount(this.favoriteLoaded, this.form.value);
  }

  public setConfig(config: SendOtherBankConfigProvider) {
    this.config = config;
  }

  public buildForm(pocket?: Pocket) {
    this.crateControls(pocket);
    this.form = new FormGroup({
      name: this.nameControl,
      documentType: this.documentTypeControl,
      documentNumber: this.documentNumberControl,
      bank: this.bankControl,
      accountType: this.accountTypeControl,
      accountNumber: this.accountNumberControl,
      amount: this.amountControl,
      [FAVORITE_CONTROL_NAME]: this.isFavoriteControl,
      pocket: this.pocketControl,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
    this.subscribeToFavoriteChanges();
  }

  public destroy() {
    this.subscriptions$?.forEach?.((subscription) => subscription?.unsubscribe?.());
  }

  public createDocumentTypeList(documentTypeTexts) {
    this.documentTypes = SendMoneyOtherBankConfig.documentTypes.map((type) => {
      return { name: documentTypeTexts[type.type], value: type.value };
    });
  }

  public buildArrayConfirmationItems(
    messages: any,
    currencyPipe: CurrencyPipe
  ): Array<ConfirmInfoItem> {
    const items: Array<ConfirmInfoItem> = [
      {
        label: messages.items.name,
        value: this.form.value.name,
      },
      {
        label: messages.items.documentType,
        value: this.form.value.documentType.name,
      },
      {
        label: messages.items.documentNumber,
        value: this.form.value.documentNumber,
      },
      {
        label: messages.items.bank,
        value: this.form.value.bank.name,
      },
      {
        label: messages.items.accountType,
        value: this.form.value.accountType.description,
      },
      {
        label: messages.items.accountNumber,
        value: this.form.value.accountNumber,
      },
      {
        label: messages.items.amount,
        value: currencyPipe.transform(this.form.value.amount),
      },
    ];

    if (this.form.value.isFavorite) {
      items.splice(items.length - 1, 0, {
        label: messages.items.state.label,
        value: messages.items.state.value,
      });
    }

    return items;
  }

  public updateAccountControlValidators(attribute: AttributeEntity) {
    this.accountNumberControl.reset();
    this.accountNumberControl.clearValidators();

    this.accountNumberControl.setValidators([
      Validators.required,
      Validators.pattern(CustomInputValidators.patterns.onlyNumbers),
      this.validateAccountLength(attribute.validators?.validAccountSizes),
    ]);

    this.accountNumberControl.updateValueAndValidity();
  }

  public setFavorite(favorite: FavoriteEntity) {
    const documentType = this.documentTypes.find(
      (document) => document.value === favorite.beneficiaryDocument.type
    );

    const accountType = favorite.bank?.accountTypes?.find?.((type) =>
      filterAccountType(type, favorite.destination.typeAccount)
    );

    this.nameControl.setValue(favorite.destination.name);
    this.documentTypeControl.setValue(documentType);
    this.documentNumberControl.setValue(favorite.beneficiaryDocument.id);
    this.bankControl.setValue(favorite.bank ?? null);
    this.accountTypeControl.setValue(accountType);
    this.accountNumberControl.setValue(favorite.destination.account);
    this.isFavoriteControl.setValue(true, { emitEvent: false });

    this.accountNumberControl.setValidators([
      Validators.required,
      Validators.pattern(CustomInputValidators.patterns.onlyNumbers),
      this.validateAccountLength(accountType?.attributes?.validators?.validAccountSizes),
    ]);

    this.favoriteLoaded = favorite;
  }

  public updateAmountControlValidator(maxMoneyToTransfer: number) {
    if (this.amountControl.value) {
      this.amountControl.reset();
      this.amountControl.clearValidators();
    }

    if (maxMoneyToTransfer) {
      this.amountControl.setValidators([
        Validators.required,
        Validators.min(1),
        Validators.max(maxMoneyToTransfer),
      ]);

      this.amountControl.updateValueAndValidity();

      return;
    }

    this.amountControl.setValidators([Validators.required, Validators.min(1)]);
    this.amountControl.updateValueAndValidity();
  }

  private crateControls(pocket?: Pocket) {
    this.nameControl = new FormControl('', [
      Validators.required,
      Validators.minLength(this.config.nameMinLength),
      Validators.maxLength(this.config.nameMaxLength),
      Validators.pattern(CustomInputValidators.patterns.onlyAlphaNumericWithSpaces),
    ]);

    this.documentTypeControl = new FormControl(null, [Validators.required]);
    this.bankControl = new FormControl(null, [Validators.required]);
    this.accountTypeControl = new FormControl(null, [Validators.required]);
    this.amountControl = new FormControl(null, [Validators.required, Validators.min(1)]);
    this.isFavoriteControl = new FormControl(false);
    this.pocketControl = new FormControl(pocket);

    this.documentNumberControl = new FormControl('', [
      Validators.required,
      Validators.minLength(this.config.documentNumberMinLength),
      Validators.maxLength(this.config.documentNumberMaxLength),
      Validators.pattern(CustomInputValidators.patterns.onlyNumbers),
    ]);

    this.accountNumberControl = new FormControl('', [
      Validators.required,
      Validators.pattern(CustomInputValidators.patterns.onlyNumbers),
    ]);
  }

  private validateAccountLength(validateAccountSizes: string): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      if (!control.value) {
        return null;
      }

      const validateAccountSizesArray = validateAccountSizes.split(',');

      const isValidAccountLength = validateAccountSizesArray.some(
        (item) => control.value.length === Number(item)
      );

      return isValidAccountLength ? null : { invalidAccountLength: true };
    };
  }

  private subscribeToFavoriteChanges() {
    this.subscriptions$ = Object.keys(this.form.controls).map((control) => {
      if (control === FAVORITE_CONTROL_NAME) {
        return null;
      }

      return this.form.controls[control].valueChanges.subscribe((value) => {
        if (this.favoriteLoaded) {
          this.isFavoriteControl.setValue(
            isSameAccount(this.favoriteLoaded, { ...this.form.value, [control]: value }),
            {
              emitEvent: false,
            }
          );
        }
      });
    });
  }
}
