import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import {
  ContactInvitationService
} from '@nequi/apps/base/mobile/config/features/commons/services/contact-invitation';
import {
  AnalyticsInteractor,
  AnalyticsType,
  OperationType,
  ProvisioningInteractor,
  Transaction
} from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import { PhonePipe } from '@nequi/features/commons/presentation/';
import { ModuleConfigProvider, UserInfoProvider } from '@nequi/features/commons/providers';
import { ContactEntity, ContactsInteractor } from '@nequi/features/contacts/core';
import { ContactControlComponent } from '@nequi/features/contacts/presentation';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';
import { NotificationInteractor } from '@nequi/features/notifications/core';
import { ProfileModel } from '@nequi/features/profile/data';
import { SendMoneyTransaction, SendMoneyUserEntity } from '@nequi/features/send/core';
import { CardModalService, FirstTimeMessageBottomSheetService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';
import { isTypeOf } from '@nequi/utils/types';

import { SendConfigProvider } from '../../../config';
import { SendMoneyParams } from '../../../core/params/send-money.params';
import { SendMoneyState } from '../../entities/send-money.state';
import { AnalyticsAction, AnalyticsIntention, SendMoneyUserConfig } from './send-money-user.config';
import { SendMoneyUserViewModel } from './send-money-user.view-model';

@Component({
  selector: 'app-send-money-user',
  templateUrl: './send-money-user.page.html',
  styleUrls: ['./send-money-user.page.scss'],
})
export class SendMoneyUserPage extends TransactionBasePage implements OnInit {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;
  @ViewChild(ContactControlComponent) public contactControlComponent: ContactControlComponent;

  public viewModel = new SendMoneyUserViewModel(this.userInfoProvider);
  public config = SendMoneyUserConfig;
  public balanceToogle = false;
  public maskedName: string;
  private contact: ContactEntity;

  constructor(
    private cardModalService: CardModalService,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private profileModel: ProfileModel,
    private contactInvitationService: ContactInvitationService,
    private currencyPipe: CurrencyPipe,
    private provisioningManagementInteractor: ProvisioningInteractor,
    private contactsInteractor: ContactsInteractor,
    private phonePipe: PhonePipe,
    private router: Router,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private sendConfigProvider: SendConfigProvider,
    private userInfoProvider: UserInfoProvider,
    private notificationInteractor: NotificationInteractor,
    injector: Injector
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public async ngOnInit() {
    const sendInfo = this.router.getCurrentNavigation().extras.state as SendMoneyState;

    this.viewModel.setConfig(this.sendConfigProvider.sendToNequiConfig);
    this.viewModel.initForm(sendInfo);

    if (sendInfo.initTransactionOnLoad && this.viewModel.form.valid) {
      this.sendMoney(sendInfo.contact);
    }
  }

  public async onSendMoney() {
    await this.setContact();

    if (this.contactControlComponent.isASelectedContact) {
      this.openBottomSheet();

      return;
    }

    this.showAreYouSureModal();
  }

  public onChangePhone() {
    if (this.viewModel.contact.valid) {
      const message = this.translateProvider.get(this.config.i18n.reviewInformation);

      this.toastService.showInfo(message, this.config.toastDuration);
    }
  }

  protected async getConfirmationInfo(_transaction: Transaction) {
    const confirmBottomSheet = this.translateProvider.get(
      SendMoneyUserConfig.i18n.confirmBottomSheet
    );

    const items = await this.buildConfirmData(confirmBottomSheet);

    return {
      title: confirmBottomSheet.title,
      confirmLabel: confirmBottomSheet.buttons.accept,
      cancelLabel: confirmBottomSheet.buttons.cancel,
      viewCancel: true,
      items,
    };
  }

  protected async showConfirmation(transaction: Transaction): Promise<boolean> {
    const info = await this.getConfirmationInfo(transaction);

    if (!info) {
      return true;
    }

    const { data } = await this.confirmBottomSheetService.show(
      await this.updateConfirmationWithPocket(info, transaction)
    );

    if (typeof data === 'boolean') {
      this.setAnalyticsIntention(
        AnalyticsIntention.CONFIRMATION,
        data ? AnalyticsAction.CONFIRM : AnalyticsAction.CORRECT
      );
    }

    return data === true;
  }

  protected async setOriginMoney(transaction: Transaction) {
    if (transaction.pocket === null) {
      const pocket = this.originMoneyService.getAvailablePocket();

      transaction.withPocket(pocket);
    }

    return transaction;
  }

  protected async onTransactionSuccess(
    response: SendMoneyUserEntity | any,
    transaction: SendMoneyTransaction
  ) {
    this.setAnalytics();

    if (this.viewModel.isSendFromRequest) {
      this.notificationInteractor.archiveNotification(
        this.viewModel.sendInfo?.request?.notificationId
      );
    }

    if (!transaction.data.contact.isNequi) {
      this.showInviteModal();

      return;
    }

    const toastMessage = this.translateProvider.get(SendMoneyUserConfig.i18n.successMessage);

    if (isTypeOf<SendMoneyUserEntity>(response, ['reference', 'amount'])) {
      this.navigatorProvider.navigateBack(SendMoneyUserConfig.routes.transactionDetail, {
        state: {
          response,
          transaction,
        },
      });
    } else {
      await this.navigatorProvider.navigateForward(SendMoneyUserConfig.routes.movements);
      this.toastService.showSuccess(toastMessage);
    }
  }

  private async setContact() {
    this.maskedName = null;
    this.contact = (await this.contactsInteractor.updateStatus([this.viewModel.contact.value]))[0];

    if (this.contact.recipient) {
      this.maskedName = this.contact.recipient;
    }
  }

  private async openBottomSheet() {
    this.viewModel.isLoading = true;

    try {
      if (this.contact.isNequi) {
        this.sendMoney(this.contact);

        return;
      }

      this.sendBottomSheet();
    } catch (error) {
      this.toastService.showError(error.message);
    } finally {
      this.viewModel.isLoading = false;
    }
  }

  private async buildConfirmData(bottomSheet) {
    const confirmData = [
      {
        label: bottomSheet.items.phone,
        value: this.phonePipe.transform(this.viewModel.contact.value.phoneNumber),
      },
      {
        label: bottomSheet.items.amount,
        value: this.currencyPipe.transform(this.viewModel.amount.value),
      },
    ];

    if (this.maskedName) {
      confirmData.splice(0, 0, {
        label: bottomSheet.items.recipient,
        value: this.maskedName,
      });
    }

    return confirmData;
  }

  private async sendBottomSheet() {
    const actualDate = new Date();
    const limitDate = new Date(actualDate.setDate(actualDate.getDate() + 1));
    const month = SendMoneyUserConfig.months[limitDate.getMonth()];

    const sendBottomSheet = this.translateProvider.get(SendMoneyUserConfig.i18n.sendBottomSheet);

    sendBottomSheet.content = createFormat(sendBottomSheet.content, [
      limitDate.getDate().toString(),
      month,
    ]);

    await this.firstTimeMessageBottomSheetService.show({
      icon: sendBottomSheet.image,
      message: sendBottomSheet.message,
      content: sendBottomSheet.content,
      cssClass: 'first-time-message--icon-standard',
      header: {
        view: true,
        action: async () => {
          this.firstTimeMessageBottomSheetService.hide();
        },
      },
      button: {
        label: sendBottomSheet.button,
        color: 'guayaba',
        fill: 'outline',
        action: async () => {
          const time = 400;

          this.firstTimeMessageBottomSheetService.hide();
          setTimeout(() => {
            this.sendMoney(this.viewModel.contact.value);
          }, time);
        },
      },
    });
  }

  private async sendMoney(contact: ContactEntity): Promise<void> {
    const transaction = new SendMoneyTransaction(
      contact.isNequi ? OperationType.SEND_MONEY_USER : OperationType.SEND_MONEY_NO_USER
    );

    const transactionData: SendMoneyParams = {
      contact: contact,
      message: this.viewModel.message.value,
      request: this.viewModel.sendInfo?.request && {
        message: this.viewModel.sendInfo.request?.message,
        transactionId: this.viewModel.transactionId.value,
      },
    };

    const transactionWith = transaction
      .withAmount(this.viewModel.amount.value)
      .withData(transactionData)
      .withPocket(this.viewModel.form.value.pocket);

    super.executeTransaction(transactionWith);
  }

  private async showInviteModal() {
    const messages = this.translateProvider.get(this.config.i18n.inviteContactModal);

    await this.cardModalService.show({
      imgPath: messages.image,
      title: messages.header,
      content: messages.message,
      buttons: [
        {
          text: messages.buttons.whatsapp,
          handler: async () => {
            this.inviteFromWhatsapp();
          },
        },
        {
          text: messages.buttons.sms,
          color: this.config.color.medianocheGray,
          handler: () => {
            this.inviteFromSms();
          },
        },
      ],
    });
  }

  private async inviteFromWhatsapp() {
    const messageTemplate = this.translateProvider.get(this.config.i18n.messageToShare);
    const amount = this.currencyPipe.transform(this.viewModel.amount.value);
    const invitationMessage = createFormat(messageTemplate, [
      this.profileModel.nickName?.toUpperCase(),
      amount,
    ]);

    await this.navigatorProvider.navigateForward(SendMoneyUserConfig.routes.notifications);

    try {
      await this.contactInvitationService.viaWhatsapp(invitationMessage);
    } catch (error) {
      this.toastService.showError(
        this.translateProvider.get(this.config.i18n.whatsappNotAvailableMessage)
      );
    }
  }

  private async inviteFromSms() {
    await this.loadingService.show();

    const params = {
      phoneNumberDestination: this.viewModel.contact.value.phoneNumber,
      phoneNumberOrigin: this.profileModel.phoneNumber,
      value: +this.viewModel.amount.value,
    };

    try {
      await this.provisioningManagementInteractor.sendSms(params);
      this.sendMessageSuccess();
    } catch (error) {
      this.sendMessageFail(error);
    }
  }

  private async sendMessageSuccess() {
    await this.loadingService.hide(true);
    this.navigatorProvider.navigateForward(SendMoneyUserConfig.routes.notifications);
  }

  private async sendMessageFail(error) {
    await this.loadingService.hide(false);
    this.toastService.showError(error.message);
  }

  private setAnalytics() {
    const analytics = SendMoneyUserConfig.analytics.events;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: analytics.successSending },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: analytics.facebookMobilePurchase },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: analytics.successSending },
    });
  }

  private async showAreYouSureModal() {
    const messages = this.translateProvider.get(this.config.i18n.areYouSureModal);
    const contentParams = [this.phonePipe.transform(this.viewModel.contact.value.phoneNumber)];

    if (this.maskedName) {
      contentParams.push(`- ${this.maskedName}`);
    }

    const content = createFormat(messages.message, contentParams);

    await this.cardModalService.show({
      imgPath: messages.image,
      title: messages.header,
      content: content,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: async () => {
            this.openBottomSheet();
            this.setAnalyticsIntention(AnalyticsIntention.CORRECTION, AnalyticsAction.CONFIRM);
          },
        },
        {
          text: messages.buttons.cancel,
          color: this.config.color.medianocheGray,
          handler: () => {
            this.setAnalyticsIntention(AnalyticsIntention.CORRECTION, AnalyticsAction.CORRECT);
          },
        },
      ],
    });
  }

  private setAnalyticsIntention(intentionName: AnalyticsIntention, action: AnalyticsAction) {
    const eventName = this.config.analytics.intention[intentionName];

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: eventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intention.params.cleverTap,
          [action, getFormattedDateTimes()]
        ),
      },
    });
  }
}
