import { Subject } from 'rxjs';

import { Injectable } from '@angular/core';
import { FavoriteEntity } from '@nequi/features/send/core';

@Injectable()
export class LoadFavoriteAccountService {
  private favoriteAccount = new Subject<FavoriteEntity>();
  // eslint-disable-next-line @typescript-eslint/member-ordering
  public favoriteAccount$ = this.favoriteAccount.asObservable();

  public setFavoriteAccount(favorite: FavoriteEntity) {
    this.favoriteAccount.next(favorite);
  }
}
