import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import { PaypalEntity } from '@nequi/features/paypal/core';
import { UnavailabilityException } from '@nequi/features/paypal/exceptions';
import { LoadingService, ToastService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { AccountService } from '../../services/account.service';
import { RegistryInfoConfig } from './registry-info-config';

@Component({
  selector: 'nequi-registry-info',
  templateUrl: './registry-info.page.html',
  styleUrls: ['./registry-info.page.scss'],
})
export class RegistryInfoPage implements OnInit {
  public config = RegistryInfoConfig;
  public data: PaypalEntity;
  public maxAllowedText;
  public taxInfo;
  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    private router: Router,
    private loadingService: LoadingService,
    private accountService: AccountService,
    private toastService: ToastService,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public async ngOnInit() {
    this.initValues();
  }

  public setRates(rates) {
    const tax = String(rates.maximumFeeValue + rates.maximumFeeValue * rates.tax);
    const fee = String(rates.maximumFeeValue * rates.maximumFeeValue * rates.fee);
    const i18Tax = 'paypal.registry.info.payment.content';

    this.taxInfo = this.translateProvider.get(i18Tax, { fee, tax });
  }

  public setLimits(limits) {
    const MIN_AVAILABLE = 20;
    const maxAllowed = limits[0].maxAllowedLimitInUSD + MIN_AVAILABLE;
    const i18nMaxAllowed = 'paypal.registry.info.receive.content.your-month';

    this.maxAllowedText = this.translateProvider.get(i18nMaxAllowed, { maxAllowed });
  }

  public goBack() {
    this.navigatorProvider.navigateBack(this.config.routes.backpackHomeUrl);
  }

  public goTo() {
    if (this.accountService.userInfo.hasAccount) {
      this.navigatorProvider.navigateForward([this.config.routes.paypalHome], {
        state: this.accountService.userInfo,
      });

      return;
    }

    this.navigatorProvider.navigateForward([this.config.routes.paypalRegistry], {
      state: this.accountService.userInfo,
    });
  }

  private async initValues() {
    await this.loadingService.show();

    this.setAnalyticsInitialIntention();

    try {
      const responseService = await this.accountService.getAccountServices();

      await this.setRates(responseService.responseRates);
      await this.setLimits(responseService.responseLimitsAccounts);
      await this.loadingService.hideWithoutFeedback();
    } catch (error) {
      if (error instanceof UnavailabilityException) {
        await this.toastService.showInfo(error.message);
      } else {
        await this.toastService.showError(error.message);
      }

      this.navigatorProvider.navigateForward(this.config.routes.backpackHomeUrl);
      await this.loadingService.hideWithoutFeedback();
    }
  }

  private setAnalyticsInitialIntention() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.initialIntention.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.initialIntention.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.initialIntention.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.initialIntention.params.cleverTap,
          [getFormattedDateTimes()]
        ),
      },
    });
  }
}
