import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { PaypalInteractor, PaypalRepository } from '@nequi/features/paypal/core';
import { FeaturesProfileModule } from '@nequi/features/profile/presentation';
import { InputSelectBottomSheetService, NequiComponentsModule, NequiUiModule } from '@nequi/ui';
import { TranslateModule, TranslatePipe } from '@ngx-translate/core';

import { PaypalConfig } from '../config/paypal.config';
import { FeaturesPaypalRoutingModule } from './features-paypal-routing.module';
import { PaypalMiddleware } from './middlewares/pay-pal.middleware';
import { FormInfoPage } from './pages/form-info/form-info.page';
import { FormPage } from './pages/form/form.page';
import { HomePage } from './pages/home/home.page';
import { RegistryInfoPage } from './pages/registry-info/registry-info.page';
import { RegistryPage } from './pages/registry/registry.page';
import { AccountService } from './services/account.service';

@NgModule({
  declarations: [FormInfoPage, RegistryInfoPage, HomePage, RegistryPage, FormPage],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    FeaturesPaypalRoutingModule,
    NequiUiModule,
    NequiComponentsModule,
    FeaturesProfileModule,
  ],
  providers: [
    {
      provide: ModuleConfigProvider,
      useValue: PaypalConfig,
    },
    {
      provide: PaypalInteractor,
      deps: [PaypalRepository],
      useClass: PaypalInteractor,
    },
    TranslatePipe,
    InputSelectBottomSheetService,
    AccountService,
    PaypalMiddleware,
  ],
})
export class FeaturesPaypalModule {}
